/* eslint-disable */
import { Scene } from 'phaser'
import GameFunction from '@/game/function/buildFunction.js'
import vuexFunction from '@//store/modules/planet.js'
import { Car } from '@/game/function/movingObject.js'

let scene
let pointer
let gameFunction
let Obj = {}
Obj.lastTile = null
Obj.lastCoordinateX = null
Obj.lastCoordinateY = null
Obj.nowScene = null
let car = []
let userData = {}
let text
let w
let h

export default class PlayScene extends Scene {
  constructor () {
    super({ key: 'PlayScene' })
  }
  init () {
    console.log('v5.7')
    w = document.body.clientWidth
    h = document.body.clientHeight
    console.log('playScene init')
    gameFunction = new GameFunction(this)

    scene = this
    scene.buildingGroup = this.add.group()

    // 設定游標
    pointer = scene.input.activePointer
    
    // 設定背景
    scene.add.image(w/2, h/2, 'bg').setDisplaySize(2*w, 2*h).setScrollFactor(0)
    
    // 畫面點擊事件
    scene.input.on('pointerdown', function(pointer) {
      vuexFunction.mutations.setBuildingElement(null, false)
      gameFunction.cancelJump()
      console.log(pointer.position.x, pointer.position.y)
    })
    // setInterval(()=>{
    //   if (pointer.downElement.tagName !== 'CANVAS') {
    //     pointer.isDown = false
    //   }
    // },100)
  }
  preload () {
    // let url
    // url = 'https://raw.githubusercontent.com/rexrainbow/phaser3-rex-notes/master/plugins/dist/rexpinchplugin.min.js'
    // this.load.plugin('rexpinchplugin', url, true)
  }
  create () {
    console.log('playScene create')
    
    scene.events.on('transitioncomplete', function(fromScene, duration){
      Obj.nowScene = 'PlayScene'
      let mapScale = userData.scale
      scene.cameras.main.setSize(w, h).centerOn(w/2, h/2+(mapScale-3)*48)
      scene.cameras.main.setZoom(0.5)
      setTimeout(() => {
        gameFunction.zoomInOut(1, 1000, 'Quart.easeOut')
      }, 250)
    })

    text = scene.add.text(10, 150, '', { font: '16px Courier', fill: '#ffffff' })

    // pinch
    // var dragScale = scene.plugins.get('rexpinchplugin').add(scene);
    // dragScale.on('drag1', function (dragScale) {
    //   var drag1Vector = dragScale.drag1Vector;
    //   scene.cameras.main.scrollX -= drag1Vector.x / scene.cameras.main.zoom;
    //   scene.cameras.main.scrollY -= drag1Vector.y / scene.cameras.main.zoom;
    // }).on('pinch', function (dragScale) {
    //   var scaleFactor = dragScale.scaleFactor
    //   scene.cameras.main.zoom *= scaleFactor
    //   if(scene.cameras.main.zoom <= 0.5){
    //     scene.cameras.main.zoom = 0.5
    //   } else if (scene.cameras.main.zoom >= 2){
    //     scene.cameras.main.zoom = 2
    //   }
    // }, scene)

    car.push(scene.add.existing(new Car(scene, 'car', 1, 1, 'drive', 'southEast', 400).setOrigin(0.5, 1)))
    car.push(scene.add.existing(new Car(scene, 'car', 2, 3, 'drive', 'northWest', 300).setOrigin(0.5, 1)))
    car.push(scene.add.existing(new Car(scene, 'senior', 0, 1, 'seniorWalk', 'seniorSE', 200).setOrigin(0, 1)))

    gameFunction.buildMap('tiles')
 
    placeBuildings()
    scene.add.image(w/2, h/2-1.5*96-5, 'indicator').setDepth(1000)

    // set camera init position
    let mapScale = userData.scale
    scene.cameras.main.setSize(w, h).centerOn(w/2, h/2+(mapScale-3)*48)
    // gameFunction.initZoomIn()
    scene.cameras.main.setZoom(0.5)
    setTimeout(() => {
      gameFunction.zoomInOut(1, 1000, 'Quart.easeOut')
    }, 250)
  }

  update () {
    car.forEach(function (car) { car.update() })
    text.setText(Math.floor(scene.game.loop.actualFps))

    // console.log(pointer.isDown , pointer.downElement.tagName)
    
    // drag camera
    pointer = scene.input.activePointer
    if (pointer.isDown) {
      if (pointer.getDistance() > 10) {
        let x = scene.cameras.main.midPoint.x - pointer.x + pointer.prevPosition.x
        let y = scene.cameras.main.midPoint.y - pointer.y + pointer.prevPosition.y
        scene.cameras.main.centerOn(x, y)
      }
    }
    // console.log(pointer.isDown)
  }
}

function getBuildingMap (scale, map) {
  userData.scale = scale
  userData.buildingMap = map
}

function placeBuildings () {
  userData.buildingMap.map(ele => {
    if (ele.status === 'ing') {
      if (ele.finishTime <= Date.now()){
        console.log('ing改completed')
        ele.status = 'completed'
        delete ele.finishTime
      }
    }
    
    switch (ele.status) {
      case 'done':
        gameFunction.createBuilding(ele.id, ele, false)
        break
      case 'completed':
        gameFunction.createBuilding('b1', ele, false)
        break
      case 'ing':
        gameFunction.createBuilding('b0', ele, false)
        break
    }
  })
}

export { w, h, Obj, userData, gameFunction, getBuildingMap, pointer }