<template lang="pug">
  div(class="notification-container" @click="closeNotificatoin")
    p(v-html="msg")
</template>

<script>
import { mapMutations } from 'vuex'

export default {
  props: {
    msg: String
  },
  data () {
    return {
      timer: null
    }
  },
  beforeMount () {
    this.timer = setTimeout(() => {
      this.CLOSE_NOTIFICATION()
    }, 3000)
  },
  methods: {
    ...mapMutations('feature', ['CLOSE_NOTIFICATION']),
    closeNotificatoin () {
      this.CLOSE_NOTIFICATION()
      clearTimeout(this.timer)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.notification-container {
  background-color: rgba(14, 3, 20, 0.84);
  box-shadow: map-get($shadow, secondary-4);
  height: 80px;
  position: fixed;
  z-index: 100;
  top: 294px;
  left: 0px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  > p {
    font-size: 12px;
    line-height: 16px;
    text-shadow: map-get($shadow, secondary-4);
  }
}
</style>
