<template lang="pug">
  div(class="menu-wrapper")
    transition-group(:name="user.isOnMainPlanet ? 'menu-on-main' : 'menu-on-game'" tag="div" class="menu-transition-wrapper")
      div(
        v-if="isModalOpen"
        v-for="(feature, index) in features"
        class="menu-item"
        :key="`${feature.title}-${index}`"
        @click="openModal(feature.title, $event)")
        router-link(v-if="feature.title === '採集'" tag="button" :ref="feature.ref" @click="emit('nextScenes', '採集頁面')" :to="user.isBeginner ? '/beginner_collect' : '/collect'" class="icon-button" :style="{'background-image': `url(${feature.icon})`}")
        button(v-else class="icon-button" :ref="feature.ref" :style="{'background-image': `url(${feature.icon})`}")
        label {{feature.title}}
</template>

<script>
import { mapGetters } from 'vuex'
import beginnerGuideInfo from '@/assets/data/beginner-guide-info.json'
export default {
  props: {
    isModalOpen: Boolean,
    user: Object
  },
  data: function () {
    return {
      module: null,
      actions: beginnerGuideInfo.actions
    }
  },
  created () {
    import('@/game/scenes/PlayScene').then(async (module) => {
      this.module = module
    })
  },
  mounted () {
    this.$store.watch((state, getters) => {
      return getters['user/getCurrentBeginnerGuideScenes']
    }, (newValue, oldValue) => {
      if (this.isBeginner) {
        if (this.actions[newValue] === '建造頁面') {
          this.$refs.buildBtn[0].classList.add('highlight')
        }
        if (this.actions[newValue] === '採集頁面') {
          this.$refs.collectBtn[0].$el.classList.add('highlight')
        }
      }
    }, { deep: true }
    )
  },
  computed: {
    ...mapGetters('user', {
      isBeginner: 'getIsBeginner',
      currentScene: 'getCurrentBeginnerGuideScenes'
    }),
    features () {
      if (this.user.isOnMainPlanet) {
        return [
          {
            title: '資源',
            icon: require('@/assets/icon/warehouse.png'),
            ref: 'resourceBtn'
          },
          {
            title: '任務',
            icon: require('@/assets/icon/mission.png'),
            ref: 'missionBtn'
          },
          {
            title: '商城',
            icon: require('@/assets/icon/gift.png'),
            ref: 'mallBtn'
          }
        ]
      } else {
        return [
          {
            title: '資源',
            icon: require('@/assets/icon/warehouse.png'),
            ref: 'resourceBtn'
          },
          {
            title: '採集',
            icon: require('@/assets/icon/collect.png'),
            ref: 'collectBtn'
          },
          {
            title: '建造',
            icon: require('@/assets/icon/build.png'),
            ref: 'buildBtn'
          },
          {
            title: '任務',
            icon: require('@/assets/icon/mission.png'),
            ref: 'missionBtn'
          },
          {
            title: '商城',
            icon: require('@/assets/icon/gift.png'),
            ref: 'mallBtn'
          }
        ]
      }
    }
  },
  methods: {
    openModal (title, event) {
      // document.getElementsByClassName('menu-transition-wrapper')[0].style.pointerEvents = 'none'
      // setTimeout(() => {
      //   document.getElementsByClassName('menu-transition-wrapper')[0].style.pointerEvents = 'auto'
      // }, 0)
      // event.target.style.pointerEvents = 'none'
      this.$emit('openModal', title + '頁面')
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.menu-wrapper {
  width: 100%;
  font-weight: 900;
  background-color: transparent;
  .menu-transition-wrapper {
    width: 100%;
    display: flex;
    justify-content: space-around;
    .menu-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      flex: 1;
      > button {
        width: 37px;
        height: 37px;
        box-sizing: border-box;
        z-index: 5;
        outline: none;
        background-position: 48% 50%;
      }
      > label {
        pointer-events: none;
        margin-top: 6px;
        font-size: 12px;
        text-shadow: map-get($shadow, primary-4);
      }
    }
    .menu-on-game-enter-active {
      transition: all 0.5s;
    }
    .menu-on-game-leave-active {
      transition: all 0.8s;
    }
    .menu-on-game-enter,
    .menu-on-game-leave-to {
      opacity: 0;
      &:nth-child(1) {
        transform: translateX(162.5px);
      }
      &:nth-child(2) {
        transform: translateX(81.5px);
      }
      &:nth-child(3) {
        transform: translateX(0);
      }
      &:nth-child(4) {
        transform: translateX(-81.5px);
      }
      &:nth-child(5) {
        transform: translateX(-162.5px);
      }
    }
    .menu-on-main-enter-active {
      transition: all 0.5s;
    }
    .menu-on-main-leave-active {
      transition: all 0.8s;
    }
    .menu-on-main-enter,
    .menu-on-main-leave-to {
      opacity: 0;
      &:nth-child(1) {
        transform: translateX(145px);
      }
      &:nth-child(2) {
        transform: translateX(0);
      }
      &:nth-child(3) {
        transform: translateX(-145px);
      }
    }
  }
}
</style>
