/* eslint-disable */
import Phaser from 'phaser'
import { w, h } from '@/game/scenes/PlayScene.js'

const directions = {
  southEast: { offset: 0, x: 2, y: 1, opposite: 'northWest' },
  northWest: { offset: 20, x: -2, y: -1, opposite: 'southEast' },
  // southWest: { offset: 224, x: -2, y: 1, opposite: 'northEast' },
  // northEast: { offset: 96, x: 2, y: -1, opposite: 'southWest' },
  seniorSE: { offset: 0, x: 2, y: 1, opposite: 'seniorNW' },
  seniorNW: { offset: 2, x: -2, y: -1, opposite: 'seniorSE' },
  // seniorSW: { offset: 4, x: -2, y: 1, opposite: 'seniorNE' },
  // seniorNE: { offset: 6, x: 2, y: -1, opposite: 'seniorSW' }
}
const anims = {
  drive: { startFrame: 0, endFrame: 19, updateRate: 0.02, speed: 0.5 },
  seniorWalk: { startFrame: 0, endFrame: 2, updateRate: 0.5, speed: 0.15 }
}

export let Car = new Phaser.Class({
  Extends: Phaser.GameObjects.Image,
  initialize:
  function Car (scene, movingObj, coordinateX, coordinateY, motion, direction, distance) {
    const origX = w / 2
    const origY = h / 2 - 1.5 * 96
    const offsetX = 96
    const offsetY = 96 / 2
    this.scene = scene
    this.movingObj = movingObj
    this.startX = origX + coordinateX * offsetX - coordinateY * offsetX
    this.startY = origY + coordinateX * offsetY + coordinateY * offsetY
    this.distance = distance
    this.motion = motion
    this.anim = anims[motion]
    this.direction = directions[direction]
    this.f = this.anim.startFrame

    Phaser.GameObjects.Image.call(this, scene, this.startX, this.startY, this.movingObj, this.direction.offset + this.f)
    this.depth = this.startY + 192 + 48
    scene.time.delayedCall(this.anim.updateRate * 1000, this.changeFrame, [], this)
    
    // click event on movingObj
    this.setInteractive()
    this.on('pointerdown', function(){
      console.log(this)
    })
  },
  changeFrame: function () {
    this.f++
    let delay = this.anim.updateRate
    if (this.f === this.anim.endFrame) {
      switch (this.motion) {
        case 'drive':
          this.f = this.anim.startFrame
          this.frame = this.texture.get(this.direction.offset + this.f)
          this.scene.time.delayedCall(delay * 1000, this.changeFrame, [], this)
          break
        default:
          this.f = this.anim.startFrame
          this.frame = this.texture.get(this.direction.offset + this.f)
          this.scene.time.delayedCall(delay * 1000, this.changeFrame, [], this)
          break
      }
    } else {
      this.frame = this.texture.get(this.direction.offset + this.f)
      this.scene.time.delayedCall(delay * 1000, this.changeFrame, [], this)
    }
  },
  resetAnimation: function () {
    this.f = this.anim.startFrame
    this.frame = this.texture.get(this.direction.offset + this.f)
    this.scene.time.delayedCall(this.anim.updateRate * 1000, this.changeFrame, [], this)
  },
  update: function () {
    if (this.motion === 'drive' || this.motion === 'seniorWalk') {
      this.x += this.direction.x * this.anim.speed

      if (this.direction.y !== 0) {
        this.y += this.direction.y * this.anim.speed
        this.depth = this.y + 192 + 48
      }

      // drive far enough?
      if (Phaser.Math.Distance.Between(this.startX, this.startY, this.x, this.y) >= this.distance) {
        this.direction = directions[this.direction.opposite]
        this.f - this.anim.startFrame
        this.frame = this.texture.get(this.direction.offset + this.f)
        this.startX = this.x
        this.startY = this.y
      }
    }
  }
})
