<template lang="pug">
  div(class="build-mode")
    p 請點擊地盤興建建築
    div(v-if="isErr" class="err-msg")
      p 此地盤已有建築或位於地圖邊緣，無法興建
    div(class="button-group")
      div
        button(@click="close" class="icon-button" :style="{'background-image': `url(${require('@/assets/icon/leave-cross.png')})`}")
        span 離開
      div
        button(@click="build" class="icon-button" ref="buildBtn" :style="{'background-image': `url(${require('@/assets/icon/building-confirm.png')})`}")
        span 建造
</template>

<script>
import beginnerGuideInfo from '@/assets/data/beginner-guide-info.json'
import { mapGetters } from 'vuex'
export default {
  props: {
    building: Object
  },
  data () {
    return {
      isErr: false,
      module: null,
      actions: beginnerGuideInfo.actions
    }
  },
  created () {
    import('@/game/scenes/PlayScene').then(async (module) => {
      this.module = module
      this.module.gameFunction.setIsOnBuildMode(true)
    })
  },
  mounted () {
    if (this.isBeginner) {
      if (this.actions[this.currentScene] === '建造下去') {
        this.$refs.buildBtn.classList.add('highlight')
      }
    }
  },
  computed: {
    ...mapGetters('user', {
      isBeginner: 'getIsBeginner',
      currentScene: 'getCurrentBeginnerGuideScenes'
    })
  },
  methods: {
    async build () {
      try {
        const self = this
        const newBuilding = {
          id: this.building.id,
          name: this.building.name,
          coordinateX: this.module.Obj.lastCoordinateX,
          coordinateY: this.module.Obj.lastCoordinateY,
          status: 'ing',
          finishTime: this.setDelayTime(this.building.buildTime),
          grid: {
            row: this.building.grid.row,
            column: this.building.grid.column
          }
        }
        await this.module.gameFunction.checkIfCanBuild(newBuilding.coordinateX, newBuilding.coordinateY, newBuilding.id)
        self.close()
        self.$bus.$emit('close')
        self.$bus.$emit('nextScenes', '建造下去')
        self.$store.dispatch('planet/buildBuilding', { building: newBuilding })
          .then(async () => {
            if (newBuilding.grid.row === 1 && newBuilding.grid.column === 1) {
              self.module.gameFunction.createBuilding('b0', newBuilding, false)
            } else if (newBuilding.grid.row === 1 && newBuilding.grid.column === 2) {
              self.module.gameFunction.createBuilding('b2', newBuilding, false)
            } else if (newBuilding.grid.row === 2 && newBuilding.grid.column === 2) {
              self.module.gameFunction.createBuilding('b4', newBuilding, false)
            }
          })
          .then(async () => self.$store.dispatch('planet/gainExperience', this.building.experience))
          .then(async () => self.$store.dispatch('user/spendCoin', this.building.cost))
          .then(async () => self.$store.dispatch('user/spendResources', this.building.resources))
          .catch(console.log)
      } catch (error) {
        this.isErr = !error
        setTimeout(() => {
          this.isErr = !this.isErr
        }, 1000)
      }
    },
    close () {
      this.$emit('close')
    },
    setDelayTime (min) {
      let time = min * 60 * 1000
      let nowTime = Date.now()
      let finishTime = nowTime + time
      return finishTime
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.build-mode {
  display: flex;
  justify-content: center;
  > p:nth-child(1) {
    position: absolute;
    top: 40px;
    text-shadow: map-get($shadow, secondary-4);
  }
  > .err-msg {
    position: absolute;
    top: 266px;
    display: flex;
    width: 316px;
    height: 54px;
    align-items: center;
    justify-content: center;
    background-color: map-get($colors, black);
    opacity: 0.7;
    border: 1px solid map-get($colors, white);
    border-radius: 7.5px;
    box-shadow: map-get($shadow, secondary-4);
    > p {
      font-size: 14px;
      font-weight: 900;
    }
  }
  > .button-group {
    position: absolute;
    top: 576px;
    // FOR TEST
    // top: 460px;
    display: flex;
    justify-content: center;
    > div {
      margin: 0 10px;
      > button {
        display: block;
        width: 37px;
        height: 37px;
        color: map-get($colors, white);
      }
      > span {
        font-size: 10px;
        text-shadow: map-get($shadow, primary-4);
      }
    }
  }
}
</style>
