<template lang="pug">
  div(id="app")
    div(id="game-container")
    router-view
    level-up(v-if="isLevelUp" @close="close")
    loading(v-if="feature.isLoading")
    notification(v-if="feature.isShowNotification" :msg="feature.notificationMsg")
</template>

<script>
import LevelUp from '@/components/LevelUp.vue'
import Loading from '@/components/Loading.vue'
import Notification from '@/components/Notification.vue'
import planetLevelInfo from '@/assets/data/planet-level-info.json'
import { mapState, mapActions, mapMutations } from 'vuex'

export default {
  components: {
    LevelUp,
    Loading,
    Notification
  },
  data () {
    return {
      isLevelUp: false,
      isFirstTime: true,
      module: null,
      planetLevelInfo: planetLevelInfo.result,
      level: 1
    }
  },
  created: function () {
    import('@/game/scenes/PlayScene').then((module) => {
      this.module = module
    })
    setInterval(() => {
      if (!this.isFirstTime) {
        this.generate_population_coin()
      }
    }, 6 * 1000)
  },
  beforeMount () {
    import('@/game/game').then(game => {
      this.$nextTick(() => game.launch())
    })
  },
  mounted () {
    this.$store.watch((state, getters) => {
      return state.planet.planet.level
    }, (newValue, oldValue) => {
      if (this.isFirstTime) {
        this.isFirstTime = false
        console.log('first')
        return
      }
      if (newValue !== oldValue) {
        console.log('level-up')
        this.level = newValue
        this.isLevelUp = true
      }
    }, { deep: true }
    )
  },
  computed: {
    ...mapState('feature', {
      feature: state => state
    }),
    ...mapState('user', {
      user: state => state
    }),
    ...mapState('planet', {
      value: state => state.planet.value
    }),
    ...mapState('planet', {
      population: state => state.planet.population
    })
  },
  watch: {
    isFirstTime () {
      const perSixMin = (new Date() - this.user.lastLogoutTime) / 1000 / 60 / 6
      // console.log('perSixMin:' + perSixMin + ' newDate:' + new Date() + ' lastLogout:' + this.user.lastLogoutTime)
      const perSixMinInt = Math.floor(perSixMin)
      const perSixMinFloat = perSixMin - perSixMinInt
      for (let i = 0; i < perSixMinInt; i++) {
        this.generate_population_coin()
      }
      this.COUNT_COIN_PRODUCTION({
        value: this.value,
        population: this.population
      })
      this.ADD_COIN(parseInt((this.user.coin.roduction / 10) * perSixMinFloat, 10))
      // console.log('First last coinProduction: ' + this.user.coin.production)
      // console.log('First last coin: ' + this.user.coin.amount)
    }
  },
  methods: {
    ...mapActions('user', ['gainDiamond']),
    ...mapActions('planet', ['expandScale', 'gainArchitectureTechnologyPoint']),
    ...mapMutations('planet', ['ADD_POPULATION']),
    ...mapMutations('user', ['ADD_COIN', 'COUNT_COIN_PRODUCTION']),
    close () {
      this.isLevelUp = false
      if (this.planetLevelInfo[this.level - 1].scale) {
        this.expandScale()
          .then(() => this.module.gameFunction.tileScaleUp())
      }
      this.gainDiamond({ diamond: 1 })
      this.gainArchitectureTechnologyPoint({ point: this.planetLevelInfo[this.level - 1].point })
    },
    generate_population_coin () {
      this.COUNT_COIN_PRODUCTION({
        value: this.value,
        population: this.population
      })
      // console.log('coinProduction: ' + this.user.coin.production + ' population:' + this.population.amount)
      this.ADD_COIN(parseInt((this.user.coin.production / 10), 10))
      // console.log('coin: ' + this.user.coin.amount)
      this.ADD_POPULATION()
    }
  }

}
</script>

<style lang="scss">
@import "@/assets/scss/main.scss";
@import "@/assets/scss/variables.scss";

#app {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: map-get($colors, white);
}
#game-container {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}
</style>
