<template lang="pug">
  div(class="stock-detail-root")
    div(class="heading-default")
      h2 {{stock.title}}
      span 擁有數：{{amount}}
    div
      p {{stock.description}}
      div
        img(:src="transformImage.things(stock.name_zh)" :class="{'biggerImg': !showBottomInfo}")
        div(v-if="showRarity")
          span 稀有度:&nbsp;
          i(v-for="n in stock.rarity" :style="{'background-image': `url(${require('@/assets/icon/star-purple.png')})`}")
    div
      div(class="heading-default")
        h4 取得路徑
      div
        p(v-for="(way,index) in stock.acquisition") {{index+1}}.{{way}}
    div(v-if="showBottomInfo" class="button-group")
      div
        button(class="icon-button" :style="{'background-image': `url(${require('@/assets/icon/gift.png')})`}")
        span 商城
      div
        button(class="icon-button" :style="{'background-image': `url(${require('@/assets/icon/build.png')})`}")
        span 交易
      div
        button(class="icon-button" :style="{'background-image': `url(${require('@/assets/icon/collect.png')})`}")
        span 採集
</template>
<script>
import transformImage from '@/assets/javascript/transformImage.js'
import things from '@/assets/data/things-information.json'
import { mapState } from 'vuex'

export default {
  props: {
    stockId: String,
    showBottomInfo: Boolean,
    showRarity: Boolean
  },
  data () {
    return {
      transformImage: transformImage,
      stock: {},
      amount: 0
    }
  },
  created () {
    const type = this.stockId.split('')[0]
    const index = Number(this.stockId.split('')[1])
    switch (type) {
      case 'm':
        this.stock = things.money[index]
        if (index === 0) {
          this.amount = this.user.diamond
        }
        if (index === 1) {
          this.amount = this.user.coin.amount
        }
        break
      case 'r':
        this.stock = things.resource[index]
        this.amount = this.user.resources[index].amount
        break
      case 'e':
        this.stock = things.expendables[index]
        this.amount = this.countBaggageStockAmount(this.user.baggage.expendables)
        break
      case 's':
        this.stock = things.sketch[index]
        this.amount = this.countBaggageStockAmount(this.user.baggage.sketch)
        break
      case 'g':
        this.stock = things.goods[index]
        this.amount = this.countBaggageStockAmount(this.user.baggage.goods)
        break
    }
  },
  computed: {
    ...mapState('user', {
      user: state => state
    })
  },
  methods: {
    countBaggageStockAmount (baggage) {
      return baggage.reduce((total, item) => {
        if (item.id === this.stockId) {
          total += 1
        }
        return total
      }, 0)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.stock-detail-root {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 37px auto 110px 20px 1fr;
  grid-template-areas:
    "heading"
    "stock"
    "acquisition"
    "."
    "button";
  > div:nth-child(1) {
    grid-area: heading;
    position: relative;
    > span {
      font-size: 10px;
      position: absolute;
      right: 0px;
      bottom: 13px;
    }
  }
  > div:nth-child(2) {
    grid-area: stock;
    display: grid;
    grid-template-rows: 17px 64px auto 25px;
    grid-template-areas:
      "."
      "description"
      "img"
      ".";
    justify-items: center;
    > p {
      font-size: 11px;
      grid-area: description;
    }
    > div {
      grid-area: img;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      > img {
        height: 153px;
      }
      > div {
        padding-top: 8px;
        display: flex;
        align-items: center;
        > span {
          font-size: 10px;
          color: map-get($colors, white);
        }
        > i {
          width: 10px;
          height: 10px;
        }
      }
      .biggerImg {
        height: 203px;
      }
    }
  }
  > div:nth-child(3) {
    grid-area: acquisition;
    display: grid;
    grid-template-rows: 23px 1fr;
    > div:nth-child(2) {
      align-self: center;
      text-align: left;
      padding-left: 13px;
      font-size: 12px;
    }
  }
  > div:nth-child(4) {
    grid-area: button;
    display: flex;
    justify-content: center;
    > div {
      margin: 0 10px;
      > button {
        display: block;
        width: 29px;
        height: 29px;
        color: map-get($colors, white);
      }
      > span {
        font-size: 10px;
        text-shadow: map-get($shadow, primary-4);
      }
    }
  }
}
</style>
