import buildingInfo from '@/assets/data/building-info.json'
import planetLevelInfo from '@/assets/data/planet-level-info.json'
import user from '@/store/modules/user.js'

const state = {
  buildingElement: '',
  isSelectBuilding: false,
  planet: {
    pid: 1,
    type: '奎納爾',
    name: '哈馬特爾斯拉',
    level: 0,
    experience: 120,
    nextLevel: 300,
    image: require('@/assets/image/planet/planet-1.png'),
    positionX: 23.505,
    positionY: 120.385,
    architectureTechnologyPoint: 1,
    architectureTechnology: {
      commonBuilding: {
        c0: true
      },
      militaryBuilding: {},
      economicBuilding: {},
      specialBuilding: {
        special: {
          s0: true,
          sl: true
        },
        historical: {}
      }
    },
    scale: 3,
    buildingMap: [
      {
        id: 'k0',
        name: 'stonePortal',
        coordinateX: 1,
        coordinateY: 2,
        status: 'done',
        finishTime: 1560307964011.0,
        grid: {
          row: 1,
          column: 1
        }
      }
    ],
    population: {
      max: 100,
      amount: 20,
      growth: 10
    },
    value: {
      power: 300,
      food: 100,
      entertainment: 300
    },
    activeBuffCard: [
      {
        title: '審判冥王・黑帝斯',
        description: '冥界是所有死者唯一的去處，因此黑帝斯的神話形象雖冷酷，但並無大多宗教神話中的惡神色彩，而是典型的奧林帕斯英挺男子。由於冥府位於地底，黑帝斯也被視為地下礦產的支配者。',
        buffs: {
          '基礎採集能力': '+10%',
          '星球能量數值': '+5%',
          '星球娛樂數值': '-5%'
        },
        rarity: 3
      }
    ],
    buffCards: [
      {
        title: '審判冥王・黑帝斯',
        description: '冥界是所有死者唯一的去處，因此黑帝斯的神話形象雖冷酷，但並無大多宗教神話中的惡神色彩，而是典型的奧林帕斯英挺男子。由於冥府位於地底，黑帝斯也被視為地下礦產的支配者。',
        buffs: {
          '基礎採集能力': '+10%',
          '星球能量數值': '+5%',
          '星球娛樂數值': '-5%'
        },
        rarity: 3
      }
    ]
  }
}

const getters = {
  getPlanetId () {
    return state.planet.pid
  },
  getPlanetType () {
    return state.planet.type
  },
  getArchitectureTechnology () {
    return state.planet.architectureTechnology
  },
  getArchitectureTechnologyPoint () {
    return state.planet.architectureTechnologyPoint
  },
  getScale () {
    return state.planet.scale
  },
  getBuildingMap () {
    return state.planet.buildingMap
  },
  getBuildElement () {
    return state.buildingElement
  },
  getIsSelectBuilding () {
    return state.isSelectBuilding
  },
  getPlanetLevel () {
    return state.planet.level
  },
  getPlanetExperience () {
    return state.planet.experience
  },
  getValue () {
    return state.planet.value
  },
  getPopulation () {
    return state.planet.population
  }
}

const mutations = {
  SET_PLANET (state, data) {
    Object.keys(data).forEach(key => {
      if (key === '_id') {
        state.planet.pid = data._id
        return
      }
      state.planet[key] = data[key]
    })
    if (state.planet.activeBuffCard.length >= 1) {
      const index = state.planet.buffCards.findIndex(card => card.title === state.planet.activeBuffCard[0].title)
      state.planet.buffCards.splice(index, 1)
    }
  },
  SET_ISSELECTBUILDING (state, data) {
    state.isSelectBuilding = data
    state.setBuildingElement = null
  },
  SET_BUILDINGSTATUS (state, index) {
    state.planet.buildingMap[index].status = 'done'
  },
  ADD_BUILDING (state, data) {
    console.log('push')
    state.planet.buildingMap.push(data)
  },
  REMOVE_BUILDING (state, index) {
    state.planet.buildingMap.splice(index, 1)
  },
  SET_ARCHITECTURETECHNOLOGY (state, archiTech) {
    state.planet.architectureTechnology[archiTech.type][archiTech.id] = true
  },
  SET_EXPERIENCE (state, experience) {
    state.planet.experience += experience
    if (state.planet.experience >= planetLevelInfo.result[state.planet.level].experience) {
      state.planet.level += 1
    }
  },
  SPEND_ARCHITECTURETECHNOLOGYPOINT (state, point) {
    state.planet.architectureTechnologyPoint -= point
  },
  GAIN_ARCHITECTURETECHNOLOGYPOINT (state, point) {
    state.planet.architectureTechnologyPoint += point
  },
  EXPAND_SCALE (state, scale) {
    state.planet.scale += 1
  },
  ADD_BUFFCARD (state, buffCard) {
    state.planet.buffCards.push(buffCard)
  },
  setBuildingElement (ele, isSelect) {
    state.isSelectBuilding = isSelect
    if (ele === null) {
      return
    }
    const type = ele.id.split('')[0]
    // const index = ele.id.split('')[1]
    let index = ele.id.split('')[1]
    if (ele.id.split('').length === 3) {
      index = parseInt(ele.id.split('')[1].concat(ele.id.split('')[2]))
    }

    switch (type) {
      case 'c':
        state.buildingElement = buildingInfo.commonBuilding[index]
        break
      case 'm':
        state.buildingElement = buildingInfo.militaryBuilding[index]
        break
      case 'e':
        state.buildingElement = buildingInfo.economicBuilding[index]
        break
      case 's':
        state.buildingElement = buildingInfo.specialBuilding[index]
        break
      case 'k':
        state.buildingElement = buildingInfo.kyronusBuilding[index]
        break
      default:
        break
    }

    // 給予有關 phaser圖面的資料
    state.buildingElement.status = ele.status
    state.buildingElement.coordinateX = ele.coordinateX
    state.buildingElement.coordinateY = ele.coordinateY
    state.buildingElement.finishTime = ele.finishTime
    state.buildingElement.grid.row = ele.grid.row
    state.buildingElement.grid.column = ele.grid.column
  },
  SET_POWER (state) {
    console.log('power')
    state.planet.value.power = state.planet.buildingMap.reduce((total, building) => {
      if (building.id === 'e3' && building.status === 'done') {
        total += 100
      }
      return total
    }, 0)
  },
  SET_FOOD (state) {
    console.log('food')
    state.planet.value.food = state.planet.buildingMap.reduce((total, building) => {
      if (building.id === 'e1' && building.status === 'done') {
        total += 100
      }
      return total
    }, 0)
  },
  SET_ENTERTAINMENT (state) {
    // wait for architecture
    console.log('entertainment')
    state.planet.value.entertainment = 400
  },
  SET_POPULATION_MAX (state) {
    console.log('population_max')
    state.planet.population.max = state.planet.buildingMap.reduce((total, building) => {
      if (building.id === 'e0' && building.status === 'done') {
        total += 100
      }
      console.log(total)
      return total
    }, 0)
    state.planet.population.amount = (state.planet.population.amount > state.planet.population.max) ? state.planet.population.max : state.planet.population.amount
  },
  ADD_POPULATION (state) {
    if (state.planet.population.max > state.planet.population.amount) {
      state.planet.population.amount++
    }
  },
  COUNT_RESOURCE_MAX (state) {
    const resourceMax = state.planet.buildingMap.reduce((total, building) => {
      if (building.id === 'c1' && building.status === 'done') {
        total += 10000
      }
      return total
    }, 3000)
    user.mutations.SET_RESOURCE_MAX(user.state, resourceMax)
  }
}

const actions = {
  AddPlanet: async function (context, params) {
    try {
      const result = await axios(`/planet`, {
        method: 'post',
        data: JSON.stringify({ type: params.type, name: params.name })
      })
      context.commit('SET_PLANET', result.data)
      return true
    } catch (error) {
      throw error
    }
  },
  getPlanetData: async function (context, params) {
    try {
      const result = await axios(`/planet/${params.pid}`, {
        method: 'get'
      })
      context.commit('SET_PLANET', result.data)
      return true
    } catch (error) {
      throw error
    }
  },
  async buildBuilding (context, params) {
    try {
      console.log('build')
      await axios(`/planet/${context.state.planet.pid}/building`, {
        method: 'post',
        data: JSON.stringify({ building: params.building })
      })
      context.commit('ADD_BUILDING', params.building)
      return true
    } catch (error) {
      throw error
    }
  },
  async haveBuiltBuilding (context, params) {
    try {
      const coordinateX = params.building.coordinateX
      const coordinateY = params.building.coordinateY
      const index = context.state.planet.buildingMap.findIndex(building => {
        return building.coordinateX === coordinateX && building.coordinateY === coordinateY
      })
      context.mutations.SET_BUILDINGSTATUS(context.state, index)
      // init power
      context.mutations.SET_POWER(context.state)
      context.mutations.SET_FOOD(context.state)
      context.mutations.SET_ENTERTAINMENT(context.state)
      context.mutations.SET_POPULATION_MAX(context.state)
      context.mutations.COUNT_RESOURCE_MAX(context.state)
      await axios(`/planet/${context.state.planet.pid}/building`, {
        method: 'patch',
        data: JSON.stringify({
          index,
          population: context.state.planet.population,
          value: context.state.planet.value,
          resourceMax: user.state.resourceMax,
          resources: user.state.resources
        })
      })
      return true
    } catch (error) {
      throw error
    }
  },
  async removeBuilding (context, params) {
    try {
      const coordinateX = params.building.coordinateX
      const coordinateY = params.building.coordinateY
      const index = context.state.planet.buildingMap.findIndex(building => {
        return building.coordinateX === coordinateX && building.coordinateY === coordinateY
      })
      context.commit('REMOVE_BUILDING', index)
      context.commit('SET_FOOD')
      context.commit('SET_POWER')
      context.commit('SET_ENTERTAINMENT')
      context.commit('SET_POPULATION_MAX')
      context.commit('COUNT_RESOURCE_MAX')

      await axios(`/planet/${context.state.planet.pid}/building`, {
        method: 'delete',
        data: JSON.stringify({
          index,
          population: context.state.planet.population,
          value: context.state.planet.value,
          resourceMax: user.state.resourceMax,
          resources: user.state.resources
        })
      })
      return true
    } catch (error) {
      throw error
    }
  },
  async devArchitectureTechnology (context, archiTech) {
    try {
      let type = archiTech.id.split('')[0]
      switch (type) {
        case 'c':
          type = 'commonBuilding'
          break
        case 'm':
          type = 'militaryBuilding'
          break
        case 's':
          type = 'survivalBuilding'
          break
        case 'e':
          type = 'economicBuilding'
          break
        default:
          break
      }
      context.commit('SET_ARCHITECTURETECHNOLOGY', { type, id: archiTech.id })
      context.commit('SPEND_ARCHITECTURETECHNOLOGYPOINT', archiTech.point)
      await axios(`/planet/${context.state.planet.pid}/architectureTechnology`, {
        method: 'patch',
        data: JSON.stringify({ type, id: archiTech.id })
      })
      await axios(`/planet/${context.state.planet.pid}/architectureTechnologyPoint`, {
        method: 'patch',
        data: JSON.stringify({ point: context.state.planet.architectureTechnologyPoint })
      })
      return true
    } catch (error) {
      throw error
    }
  },
  async gainArchitectureTechnologyPoint (context, params) {
    try {
      context.commit('GAIN_ARCHITECTURETECHNOLOGYPOINT', params.point)
      await axios(`/planet/${context.state.planet.pid}/architectureTechnologyPoint`, {
        method: 'patch',
        data: JSON.stringify({ point: context.state.planet.architectureTechnologyPoint })
      })
      return true
    } catch (error) {
      throw error
    }
  },
  async gainExperience (context, experience) {
    try {
      context.commit('SET_EXPERIENCE', experience)
      await axios(`/planet/${context.state.planet.pid}/experience`, {
        method: 'patch',
        data: JSON.stringify({ experience: context.state.planet.experience })
      })
      return true
    } catch (error) {
      throw error
    }
  },
  async drawBuffCard (context, params) {
    try {
      context.commit('ADD_BUFFCARD', params.buffCard)
      await axios(`/planet/${context.state.planet.pid}/buffCards`, {
        method: 'post',
        data: JSON.stringify({ buffCard: params.buffCard })
      })
    } catch (error) {
      throw error
    }
  },
  async useBuffCard (context, params) {
    try {
      await axios(`/planet/${context.state.planet.pid}/activeBuffCard`, {
        method: 'patch',
        data: JSON.stringify({ buffCard: params.activeBuffCard[0] })
      })
    } catch (error) {
      throw error
    }
  },
  async levelUp (context) {
    try {
      await axios(`/planet/${context.state.planet.pid}/level`, {
        method: 'patch'
      })
    } catch (error) {
      throw error
    }
  },
  async expandScale (context) {
    try {
      context.commit('EXPAND_SCALE')
      await axios(`/planet/${context.state.planet.pid}/scale`, {
        method: 'patch'
      })
    } catch (error) {
      throw error
    }
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
