<template lang="pug">
  div(class="robot-root")
    div(class="heading-default")
      h1 Duino
      span 擁有數： {{user.collectorMax}}
    div
      p {{content}}
      img(:src="require('@/assets/robot-normal.png')")
    div
      div(class="heading-default")
        h4 採集器狀態
      div
        template(v-for="(item,index) in user.collectorMax")
          div(v-if="!robotInfo[index].isOnCoolDown" class="waiting")
            div(:style="{'background-image': `url(${require('@/assets/robot-normal.png')})`}")
            span 待命中
          div(v-else class="cool-down")
            div(:style="{'background-image': `url(${require('@/assets/robot-cool-down.png')})`}")
            span 冷卻中 {{robotInfo[index].remainTime}}
    button(class="button-default") 直接結束冷卻
</template>
<script>
export default {
  props: {
    user: Object
  },
  data: function () {
    return {
      robotTimer: 0,
      robotInfo: [],
      content: 'Duino是人類的好朋友，面對嚴峻的波波拉，Duino擁有很好的偵測能力，可以避開危險，找到充分的資源。但是使用Duino需要一定的精神力去控制，因此除非擁有很好的能力，不然要等待一下才能使用他呢。'
    }
  },
  created () {
    let self = this
    this.robotInfo = Array.from({ length: this.user.collectorMax }).map(_ => ({ isOnCoolDown: false }))
    this.user.coolDownGrid.forEach((item, index) => {
      self.$set(this.robotInfo, index, { isOnCoolDown: true, remainTime: 0, finishTime: item.finishTime })
      self.countDown(index)
    })
    this.timer()
  },
  beforeDestroy () {
    clearInterval(this.robotTimer)
  },
  methods: {
    timer () {
      let self = this
      this.robotTimer = setInterval(() => {
        self.robotInfo.forEach((item, index) => {
          if (self.robotInfo[index].isOnCoolDown === true) {
            self.countDown(index)
          }
        })
      }, 1000)
    },
    countDown (index) {
      const end = Date.parse(new Date(this.robotInfo[index].finishTime))
      const now = Date.parse(new Date())
      const msec = end - now
      if (msec < 0) {
        this.$set(this.robotInfo[index], 'isOnCoolDown', false)
        this.robotInfo[index].remainTime = 0
        return
      }
      let day = parseInt(msec / 1000 / 60 / 60 / 24)
      let hr = String(parseInt(msec / 1000 / 60 / 60 % 24)).padStart(2, '0')
      let min = String(parseInt(msec / 1000 / 60 % 60)).padStart(2, '0')
      let sec = String(parseInt(msec / 1000 % 60)).padStart(2, '0')
      this.robotInfo[index].remainTime = day > 0 ? `${day}天` : ''
      if (this.robotInfo[index].remainTime !== 'NaN:NaN:NaN') {
        this.$set(this.robotInfo[index], 'remainTime', `${hr}:${min}:${sec}`)
      }
    }
  }
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";
.robot-root {
  display: grid;
  grid-template-rows: 11px 37px 252px 15px 141px 26px;
  grid-template-areas:
    "."
    "robot-title"
    "robot-content"
    "."
    "state"
    "button";
  > div:nth-child(1) {
    grid-area: robot-title;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    position: relative;
    padding-bottom: 13px;
    > h1 {
      font-size: 24px;
      line-height: 24px;
      text-shadow: map-get($shadow, secondary-4);
    }
    > span {
      font-size: 10px;
      line-height: 10px;
      position: absolute;
      right: 0;
    }
  }
  > div:nth-child(2) {
    grid-area: robot-content;
    display: grid;
    justify-items: center;
    align-items: center;
    grid-template-rows: 17px 42px 17px 174px;
    grid-template-areas:
      "."
      "discription"
      "."
      "img";
    > p {
      grid-area: discription;
      font-size: 10px;
    }
    > img {
      grid-area: img;
      height: 178px;
    }
  }
  > div:nth-child(3) {
    grid-area: state;
    > div:nth-child(1) {
      > h4 {
        font-size: 12px;
      }
    }
    > div:nth-child(2) {
      overflow-x: scroll;
      overflow-y: hidden;
      padding-top: 18px;
      display: grid;
      grid-auto-columns: 83px;
      grid-template-rows: 83px;
      grid-column-gap: 10px;
      grid-auto-flow: column;
      align-items: center;
      > div {
        width: 83px;
        height: 81px;
        border: solid 1px map-get($colors, white);
        border-radius: 4px;
        display: grid;
        justify-content: center;
        align-items: center;
        grid-template-rows: 12px 50px 6px 9px 1fr;
        grid-template-areas:
          "."
          "img"
          "."
          "p"
          ".";
        > div {
          grid-area: img;
          height: 50px;
          width: 52px;
          position: relative;
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
        }
        > span {
          color: map-get($colors, white);
          grid-area: p;
          font-size: 9px;
        }
      }
      .waiting {
        border: solid 1px map-get($colors, white);
        > span {
          color: map-get($colors, white);
        }
      }
      .cool-down {
        border: solid 1px #ff417c;
        > span {
          color: #ff417c;
        }
      }
    }
  }
  > button:nth-child(4) {
    justify-self: center;
    grid-area: button;
    height: 100%;
    width: 97px;
    font-size: 12px;
  }
}
</style>
