const state = {
  isShowNotification: false,
  notificationMsg: '',
  isLoading: false
}

const getters = {
  getIsLoading () {
    return state.isLoading
  }
}

const mutations = {
  SHOW_NOTIFICATION (state, msg) {
    state.isShowNotification = true
    state.notificationMsg = msg
  },
  CLOSE_NOTIFICATION (state) {
    state.isShowNotification = false
    state.notificationMsg = ''
  },
  SET_ISLOADING (state, boolean) {
    state.isLoading = boolean
  }
}
const actions = {}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
