<template lang="pug">
  div(class="warehouse-root")
    div
      div(class="heading-default")
        h3 資源庫存
    div
      div(class="heading-default")
        h4 寶石金幣
        span 金幣最大容量 {{user.coin.max}}
      div
        div(@click="showMoney('m0')")
          i(:style="{'background-image': `url(${require('@/assets/icon/diamond.png')})`}")
          span {{user.diamond}}
        div(@click="showMoney('m1')")
          i(:style="{'background-image': `url(${require('@/assets/icon/coin.png')})`}")
          span {{user.coin.amount}}
    div
      div(class="heading-default")
        h4 建築材料
        span 每一資源最大容量 {{user.resourceMax}}
      div
        div(v-for="(resource,index) in user.resources" @click="showResource(index)")
          i(:style="{'background-image':`url(${transformImage.things(resource.type)})`}")
          span {{resource.amount}}
    div
      div(class="heading-default")
        h4 背包物品
        span ! 每個背包裝載不同的物件
      div
        div(:style="{'background-image': `url(${transformImage.things('薛丁格的貓一般')})`}"  @click="showBaggage('物品背包')")
        div(:style="{'background-image': `url(${transformImage.things('薛丁格的貓一般')})`}"  @click="showBaggage('設計圖背包')")
        div(:style="{'background-image': `url(${transformImage.things('薛丁格的貓一般')})`}"  @click="showBaggage('消耗品背包')")
    modal-card-content(v-if="isShowDetail" @close="closeDetail()")
      baggage-detail(v-if="isShowBaggage" slot="card" :baggageName="baggageName")
      stock-detail(v-else slot="card" :stockId="stockId" :showBottomInfo="showBottomInfo" :showRarity="false")
</template>
<script>
import transformImage from '@/assets/javascript/transformImage.js'
import ModalCardContent from '@/components/ModalCardContent.vue'
import BaggageDetail from '@//components/game/warehouse/BaggageDetail.vue'
import StockDetail from '@/components/game/warehouse/StockDetail.vue'

export default {
  components: {
    ModalCardContent,
    StockDetail,
    BaggageDetail
  },
  props: {
    user: Object
  },
  data () {
    return {
      transformImage: transformImage,
      isShowDetail: false,
      baggageName: '',
      showBottomInfo: true,
      isShowBaggage: false,
      stockId: ''
    }
  },
  methods: {
    showBaggage (baggageName) {
      this.baggageName = baggageName
      this.isShowBaggage = true
      this.isShowDetail = true
    },
    showResource (index) {
      this.isShowDetail = true
      this.stockId = 'r' + index
      this.showBottomInfo = true
    },
    showMoney (id) {
      this.isShowDetail = true
      this.stockId = id
      this.showBottomInfo = false
    },
    closeDetail () {
      this.isShowDetail = false
      this.isShowBaggage = false
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.warehouse-root {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 50px 96px 176px auto;
  grid-template-areas:
    "heading"
    "diamond"
    "resource"
    "baggage";
  > div:nth-child(1) {
    box-sizing: border-box;
    padding-bottom: 21px;
  }
  > div:nth-child(2) {
    > div:nth-child(1) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      > span {
        font-size: 10px;
      }
    }
    > div:nth-child(2) {
      display: flex;
      padding-top: 20px;
      justify-content: space-evenly;
      > div {
        display: flex;
        align-items: center;
        > i {
          width: 28px;
          height: 34px;
          background-size: cover;
        }
        > span {
          font-size: 12px;
        }
      }
    }
  }
  > div:nth-child(3) {
    > div:nth-child(1) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      > span {
        font-size: 10px;
      }
    }
    > div:nth-child(2) {
      display: grid;
      grid-template-columns: 34px 34px 34px 34px;
      grid-template-rows: 49px 49px;
      grid-gap: 16px 36.5px;
      justify-content: center;
      padding-top: 12px;
      > div {
        i {
          width: 34px;
          height: 36px;
          background-size: contain;
        }
        span {
          display: block;
          font-size: 12px;
          line-height: 12px;
        }
      }
    }
  }
  > div:nth-child(4) {
    > div:nth-child(1) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      > span {
        font-size: 10px;
      }
    }
    > div:nth-child(2) {
      display: grid;
      grid-template-columns: 85px 85px 85px;
      grid-auto-rows: 85;
      grid-gap: 11px 8px;
      padding-top: 17px;
      > div {
        width: 85px;
        height: 85px;
        background-color: #ffffff;
        background-size: cover;
      }
    }
  }
}
</style>
