<template lang="pug">
  div(class="embassy-root")
    div(id="head")
      div
        img(src="@/assets/icon/yu-portrait.png")
      div
        h3 uid
        h6 {{user.uid}}
    div
      label(for="uid") 收尋玩家
      input(type="number" id="uid" v-model="uid")
      button(@click="search") 搜尋
    div(v-if="searchedUser")
      p 玩家名稱 {{searchedUser.username}}
      button(@click="submitAddFriend") 申請好友
    tab(
      :navList="navList"
      @switchMenu="switchMenu")
      div(slot="content")
        div(v-if="currentMenu === '好友清單'")
          template(v-for="friend in user.friends")
            h3 {{friend.username}}
        div(v-if="currentMenu === '好友邀請'")
          template(v-for="friend in user.friendInvitations")
            h3 {{friend.username}}
            button(@click="ensureAcceptFriendInvitation(friend)") 接受
</template>

<script>
import Tab from '@/components/Tab.vue'
import { mapMutations, mapActions } from 'vuex'

export default {
  components: {
    Tab
  },
  props: {
    user: Object
  },
  data () {
    return {
      navList: ['好友清單', '好友邀請'],
      currentMenu: '好友清單',
      uid: '',
      searchedUser: ''
    }
  },
  async created () {
    await this.getFriends()
    await this.getAllFriendInvitations()
  },
  sockets: {
    getAllFriendInvitations (user) {
      this.getAllFriendInvitations()
    },
    getFriends () {
      this.getFriends()
    },
    serverError (msg) {
      console.log(msg)
    }
  },
  methods: {
    ...mapMutations('feature', ['SHOW_NOTIFICATION']),
    ...mapActions('user', ['searchUser', 'getFriends', 'acceptFriendInvitation', 'getAllFriendInvitations', 'addFriend']),
    search () {
      this.searchUser({ uid: this.uid })
        .then(user => {
          this.searchedUser = user
        })
        .catch(error => {
          if (error.response.status === 404) {
            this.SHOW_NOTIFICATION('找不到此玩家')
          }
          if (error.response.status === 405) {
            this.SHOW_NOTIFICATION('此玩家尚未擁有外交大使館')
          }
        })
    },
    submitAddFriend () {
      this.$socket.emit('addFriend', {
        user: {
          uid: this.user.uid,
          username: this.user.username,
          nickname: this.user.username
        },
        friend: this.searchedUser
      })
    },
    ensureAcceptFriendInvitation (friend) {
      this.$socket.emit('acceptFriendInvitation', {
        user: {
          uid: this.user.uid,
          username: this.user.username,
          nickname: this.user.username
        },
        friend: friend
      })
      this.acceptFriendInvitation({ user: friend })
    },
    switchMenu (one) {
      this.currentMenu = one
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";
.embassy-root {
  #head {
    grid-area: avatar;
    display: flex;
    padding: 6px;
    > div:nth-child(1) {
      img {
        width: 68px;
        height: 68px;
        background-size: contain;
        border-radius: 50%;
        border: 4px solid map-get($colors, primary);
        box-shadow: 0 0 4px map-get($colors, primary);
      }
    }

    > div:nth-child(2) {
      padding-left: 23px;
      text-align: left;
      display: flex;
      flex-direction: column;
      justify-content: center;

      > h3 {
        font-size: 18px;
        line-height: 29px;
      }

      > h6 {
        font-size: 12px;
      }
    }
  }
  > div:nth-child(3) {
    grid-row-gap: 12px;
    /deep/ .nav {
      span {
        font-size: 11px;
        line-height: 11px;
      }
    }
    > div:nth-child(2) {
      height: 320px;
      overflow-y: scroll;
    }
  }
}
</style>
