import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './register-service-worker'
import 'leaflet/dist/leaflet.css'
import './bus'
import VueSocketIO from 'vue-socket.io'

const socket = new VueSocketIO({
  debug: true,
  connection: 'http://localhost:3000',
  vuex: {
    store,
    actionPrefix: 'SOCKET_',
    mutationPrefix: 'SOCKET_'
  }
})

Vue.use(socket)

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

let isBack = false
window.onpopstate = function () {
  isBack = true
}

router.beforeEach((to, from, next) => {
  if (isBack) {
    isBack = false
    return next({ path: from.path })
  }
  store.commit('feature/SET_ISLOADING', true)
  next()
})

router.afterEach((to, from) => {
  store.commit('feature/SET_ISLOADING', false)
})

window.onbeforeunload = function (e) {
  e = e || window.event
  // 兼容IE8和Firefox 4之前的版本
  if (e) {
    socket.io.emit('shutDown', {
      coin: store.getters['user/getCoin'],
      value: store.getters['planet/getValue'],
      population: store.getters['planet/getPopulation']
    })
    return null
  }
  // Chrome, Safari, Firefox 4+, Opera 12+ , IE 9+
  socket.io.emit('shutDown', {
    coin: store.getters['user/getCoin'],
    value: store.getters['planet/getValue'],
    population: store.getters['planet/getPopulation']
  })
  return null
}

