<template lang="pug">
  div(class="collect-root")
    div(v-if="isCatchLocation === 0" id="scan-anim-layer" class="cover-layer")
        video(width="350" height="360" playsinline autoplay muted loop)
          source(src="../assets/video/collect/scanning.mp4" type="video/mp4")
        //- img(src="../assets/video/collect/scanning.gif")
        div(style="font-weight: 700") 掃描地表資源中...
    //- div(v-else-if="isCatchLocation === -1" id="error-layer" class="cover-layer")
    //-   div 抓不到定位，請檢查裝置定位
    //-   router-link(tag="div" to="/game" style="font-weight: 700") 回到遊戲主畫面
    collect-panel(
      v-else
      @collect="ifCollectable"
      :user="userData"
      :collectionInfo="collectionInfo"
      :isCollectable="isCollectable")
    div(id="mapid")
    collect-report(v-if="isCollecting" @close="closeReport")
</template>

<script>
import L from 'leaflet'
import CustomDialog from '@/components/beginnerGuide/Dialog.vue'
import CollectPanel from '@/components/game/collect/CollectPanel.vue'
import CollectReport from '@/components/game/collect/CollectReport.vue'
import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'
import beginnerGuideInfo from '@/assets/data/beginner-guide-info.json'
import historicalSiteInfo from '@/assets/data/historical-site.json'
import transformImage from '@/assets/javascript/transformImage.js'

export default {
  name: 'Collect',
  components: {
    CollectPanel,
    CustomDialog,
    CollectReport
  },
  data: function () {
    return {
      isCollecting: false,
      isCatchLocation: 0,
      mymap: null,
      watcher: null,
      geoOptions: {
        enableHighAccuracy: false,
        timeout: 10000,
        maximumAge: 0,
        distanceFilter: 1
      },
      lat: null,
      lng: null,
      marker: null,
      gap: 0.005,
      geojson: null,
      visibleHistoricalSiteInfo: null,
      historicalSpot: null,
      currentHistoricalRegion: null,
      myZoom: {},
      isZoom: false,
      locationGridArr: [],
      collectionInfo: undefined,
      isCollectable: false,
      module: null,
      robotIsOpen: true,
      actions: beginnerGuideInfo.actions
    }
  },
  beforeMount: function () {
    navigator.geolocation.getCurrentPosition(this.drawMap, this.error, this.geoOptions)
    this.watcher = navigator.geolocation.watchPosition(this.update, this.updateError, this.geoOptions)
  },
  created: function () {
    import('@/game/scenes/PlayScene').then(async (module) => {
      this.module = module
      this.module.gameFunction.scene.input.manager.enabled = false
    })
  },
  beforeDestroy: function () {
    navigator.geolocation.clearWatch(this.watcher)
    this.module.gameFunction.scene.input.manager.enabled = true
  },
  computed: {
    ...mapState('user', {
      userData: state => state
    }),
    ...mapState('planet', {
      planet: state => state.planet
    }),
    ...mapGetters('map', {
      mapGridData: 'getMapGridData',
      getRespawnTime: 'getRespawnTime'
    })
  },
  methods: {
    ...mapMutations('user', ['CHECK_USER_COOLDOWN', 'NEXT_SCENES']),
    ...mapActions('user', ['collectCoolDown', 'addBaggageItems']),
    ...mapMutations('map', ['FILTER_MAPGRIDDATA', 'ADD_GRIDDATA']),
    ...mapActions('map', ['costResources', 'respawnGridData']),
    drawMap (pos) {
      setTimeout(() => {
        this.isCatchLocation = 1
      }, 2000)

      this.lat = pos.coords.latitude
      this.lng = pos.coords.longitude
      console.log(this.lat, this.lng)
      if (this.mymap) { this.mymap.remove() }
      this.mymap = L.map('mapid', {
        center: [this.lat, this.lng],
        zoom: 15,
        zoomControl: false
      })
      L.tileLayer('https://api.tiles.mapbox.com/v4/{id}/{z}/{x}/{y}.png?access_token={accessToken}', {
        // attribution: 'Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery © <a href="https://www.mapbox.com/">Mapbox</a>',
        maxZoom: 18,
        minZoom: 10,
        errorTilrUrl: 'http://bpic.588ku.com/element_pic/16/12/07/706f7ff4f15725b17ba1d30d384e6468.jpg',
        id: 'mapbox.streets',
        accessToken: 'pk.eyJ1IjoibGVva3VvMDcyNCIsImEiOiJjanZ6YzltbXUwcDVoM3pyNmI0aGp5N29tIn0.xjdaery7ZqnxkZhbktidYQ'
      }).addTo(this.mymap)

      // 玩家marker
      let myIcon = L.icon({
        iconUrl: require('@/assets/marker.png'),
        iconSize: [54, 56],
        iconAnchor: [27, 56],
        className: 'marker-icon'
      })
      this.marker = L.marker([this.lat, this.lng], { icon: myIcon }).addTo(this.mymap)
      this.drawGeoJSON()

      this.myZoom = {
        start: this.mymap.getZoom(),
        end: this.mymap.getZoom()
      }

      // 古蹟建築
      this.drawHistoricalGeoJSON()

      this.mymap.on('moveend', () => {
        if (!this.isZoom) {
          console.log('move')
          this.drawHistoricalGeoJSON()
        }
        this.isZoom = false
      })
      this.mymap.on('zoomstart', (e) => {
        this.isZoom = true
        this.myZoom.start = this.mymap.getZoom()
      })
      this.mymap.on('zoomend', (e) => {
        // console.log(this.mymap.getBounds())
        // console.log(this.mymap.getZoom())
        this.myZoom.end = this.mymap.getZoom()
        let diff = this.myZoom.end - this.myZoom.start
        if (diff < 0 && this.mymap.getZoom() < 15) {
          this.drawHistoricalGeoJSON()
        }
        this.historicalSpot.eachLayer(layer => {
          layer.setIcon(L.icon(this.setHistoricalIconSizeOptions(layer.feature)))
        })
      })
    },
    error (err) {
      console.warn('ERROR(' + err.code + '): ' + err.message)
      this.isCatchLocation = -1
      // 以下為測試用
      let pos = {
        coords: {
          longitude: 120.21575,
          latitude: 22.99962
        }
      }
      this.drawMap(pos)
    },
    updateError (err) {
      console.warn('ERROR(' + err.code + '): ' + err.message)
    },
    update (pos) {
      this.lat = pos.coords.latitude
      this.lng = pos.coords.longitude
      this.marker.setLatLng([this.lat, this.lng])

      if (this.geojson) {
        this.geojson.addData(this.createVisiableAreaData())
        // this.geojson.bringToBack()
      }
      this.historicalSpot.bringToFront()
    },
    setVisibleHistoricalSiteData () {
      // 賦值給新的變數
      this.visibleHistoricalSiteInfo = Object.assign({}, historicalSiteInfo)

      let northEast = {
        lat: this.mymap.getBounds()._northEast.lat + 0.01,
        lng: this.mymap.getBounds()._northEast.lng + 0.01
      }
      let southWest = {
        lat: this.mymap.getBounds()._southWest.lat - 0.01,
        lng: this.mymap.getBounds()._southWest.lng - 0.01
      }

      let newFeatures = historicalSiteInfo.features.filter(data => {
        return data.geometry.coordinates[0] < northEast.lng &&
          data.geometry.coordinates[0] > southWest.lng &&
          data.geometry.coordinates[1] < northEast.lat &&
          data.geometry.coordinates[1] > southWest.lat
      })
      this.visibleHistoricalSiteInfo.features = newFeatures
      return this.visibleHistoricalSiteInfo
    },
    setHistoricalIconSizeOptions (feature) { // 控制古蹟icon大小
      let options
      if (this.myZoom.end > 16) {
        options = {
          iconUrl: transformImage.building(feature.properties.name),
          iconSize: [feature.properties.initSize.width * 1.5 * (this.myZoom.end - 16), feature.properties.initSize.height * 1.5 * (this.myZoom.end - 16)],
          iconAnchor: [feature.properties.initSize.width * 1.5 * (this.myZoom.end - 16) / 2, feature.properties.initSize.height * 1.5 * (this.myZoom.end - 16) / 2]
        }
      } else if (this.myZoom.end < 14) {
        options = {
          iconUrl: transformImage.building(feature.properties.name),
          iconSize: [feature.properties.initSize.width / 1.5 / (14 - this.myZoom.end), feature.properties.initSize.height / 1.5 / (14 - this.myZoom.end)],
          iconAnchor: [feature.properties.initSize.width / 1.5 / (14 - this.myZoom.end) / 2, feature.properties.initSize.height / 1.5 / (14 - this.myZoom.end) / 2]
        }
      } else {
        options = {
          iconUrl: transformImage.building(feature.properties.name),
          iconSize: [feature.properties.initSize.width, feature.properties.initSize.height],
          iconAnchor: [feature.properties.initSize.width / 2, feature.properties.initSize.height / 2]
        }
      }
      return options
    },
    drawHistoricalGeoJSON () {
      if (this.historicalSpot) {
        this.historicalSpot.clearLayers()
      }
      this.historicalSpot = L.geoJSON(this.setVisibleHistoricalSiteData(), {
        pointToLayer: (feature, latlng) => {
          let customSpot = L.icon(this.setHistoricalIconSizeOptions(feature))
          return L.marker(latlng, { icon: customSpot })
        },
        onEachFeature: this.onEachHistoricalSpot
      }).addTo(this.mymap)
    },
    drawGeoJSON () {
      this.geojson = L.geoJSON(this.createVisiableAreaData(), {
        style: this.style,
        onEachFeature: this.onEachFeature
      }).addTo(this.mymap)

      // 設定時器更新geojson
      for (let i = 0; i < this.locationGridArr.length; i++) {
        this.updateLocalGeoJson(this.locationGridArr[i])
      }
    },
    createVisiableAreaData () {
      if (this.geojson) {
        this.geojson.clearLayers()
        this.collectionInfo = undefined
        this.isCollectable = true
      }
      this.locationGridArr = []
      this.CHECK_USER_COOLDOWN()
      this.FILTER_MAPGRIDDATA()

      let gapReciprocal = 1 / this.gap
      let maxLat = Math.ceil(this.lat * gapReciprocal) / gapReciprocal
      let maxLng = Math.ceil(this.lng * gapReciprocal) / gapReciprocal
      let range = Math.floor(this.userData.detectRange / 2)

      for (let i = -range; i <= range; i++) {
        for (let j = -range; j <= range; j++) {
          let checkLat = Math.round((maxLat + (i * this.gap)) * gapReciprocal) / gapReciprocal
          let checkLng = Math.round((maxLng + (j * this.gap)) * gapReciprocal) / gapReciprocal
          // 找已儲存陣列中是否有符合的
          const gridData = this.mapGridData.find(ele => {
            return ele.properties.maxLat === checkLat && ele.properties.maxLng === checkLng
          })
          if (gridData) { // Global的mapGridData有該格資料
            this.locationGridArr.push(gridData)
          } else {
            let basicGrid = {
              'type': 'Feature',
              'properties': {
                'maxLat': checkLat,
                'maxLng': checkLng,
                'name': 'test',
                'density': 0,
                'respawnTime': null,
                'storages': [
                  {
                    'type': '木頭',
                    'amount': 2000
                  },
                  {
                    'type': '石頭',
                    'amount': 1600
                  },
                  {
                    'type': '鋼鐵',
                    'amount': 1000
                  },
                  {
                    'type': '銅礦',
                    'amount': 400
                  }
                ]
              },
              'geometry': {
                'type': 'Polygon',
                'coordinates': [
                  [
                    [checkLng, checkLat],
                    [checkLng - this.gap, checkLat],
                    [checkLng - this.gap, checkLat - this.gap],
                    [checkLng, checkLat - this.gap],
                    [checkLng, checkLat]
                  ]
                ]
              }
            }
            this.locationGridArr.push(basicGrid)
          }
        }
      }
      return this.locationGridArr
    },
    style (feature) {
      let remainStorage = 0
      feature.properties.storages.map(ele => {
        remainStorage += ele.amount
      })
      return {
        fillColor: this.getColor(remainStorage * 100 / 5000),
        weight: 2,
        opacity: 0.7,
        color: '#000',
        dashArray: '6',
        fillOpacity: 0.2
      }
    },
    getColor (d) {
      return d < 10 ? '#680A64'
        : d < 20 ? '#8A2584'
          : d < 40 ? '#A842A4'
            : d < 60 ? '#C964C5'
              : d < 80 ? '#E881E4'
                : d < 100 ? '#FCA8FA'
                  : '#FEDDFD'
    },
    highlightFeature (e) {
      let gapReciprocal = 1 / this.gap
      let maxLat = Math.ceil(this.lat * gapReciprocal) / gapReciprocal
      let maxLng = Math.ceil(this.lng * gapReciprocal) / gapReciprocal
      let layer = e.target

      this.mymap.fitBounds(e.target.getBounds())
      this.geojson.setStyle({
        weight: 2,
        opacity: 1,
        color: 'white',
        dashArray: '3',
        fillOpacity: 0.2
      })

      layer.setStyle({
        weight: 3,
        color: '#666',
        dashArray: '',
        fillOpacity: 0.3
      })

      if (!L.Browser.ie && !L.Browser.opera && !L.Browser.edge) {
        layer.bringToFront()
      }

      if (layer.feature.properties.maxLat === maxLat && layer.feature.properties.maxLng === maxLng) {
        this.isCollectable = true
      } else {
        this.isCollectable = false
      }
      this.collectionInfo = layer.feature.properties

      // 古蹟層永遠在前面
      this.historicalSpot.bringToFront()
    },
    onEachFeature (feature, layer) {
      layer.on({
        click: this.highlightFeature
      })
    },
    onEachHistoricalSpot (feature, layer) {
      layer.bindPopup('<h3>' + feature.properties.name + '</h3>')
      layer.on('popupopen', (popup) => {
        this.currentHistoricalRegion = L.circle(layer.getLatLng(),
          {
            radius: popup.target.feature.properties.radius,
            color: '#fff',
            weight: 1,
            opacity: 1,
            dashArray: '3',
            fillColor: '#E85D75',
            fillOpacity: 0.4,
            className: 'historical-region'
          }).addTo(this.mymap)
        // this.mymap.panTo(layer.getLatLng())
        // this.mymap.setView(layer.getLatLng(), 16)
      })
      layer.on('popupclose', () => {
        this.currentHistoricalRegion.remove()
      })
    },
    ifCollectable () {
      let gapReciprocal = 1 / this.gap
      let maxLat = Math.ceil(this.lat * gapReciprocal) / gapReciprocal
      let maxLng = Math.ceil(this.lng * gapReciprocal) / gapReciprocal

      this.CHECK_USER_COOLDOWN()
      // 檢查是否有足夠的採集器
      let collectorAmount = this.userData.collectorMax - this.userData.coolDownGrid.length
      if (collectorAmount > 0) {
        console.log('收集器足夠')
        // 判斷該格有沒有在冷卻陣列中
        let status = this.userData.coolDownGrid.some(ele => { return ele.maxLat === maxLat && ele.maxLng === maxLng })
        if (status) { // 該格還在冷卻
          console.log('還在冷卻')
        } else {
          this.collect()
        }
      } else {
        console.log('收集器不夠')
      }
    },
    collect () {
      // 採集動畫與報告呈現
      this.isCollecting = true

      let gapReciprocal = 1 / this.gap
      let maxLat = Math.ceil(this.lat * gapReciprocal) / gapReciprocal
      let maxLng = Math.ceil(this.lng * gapReciprocal) / gapReciprocal
      // 加到使用者自己冷卻陣列中
      this.collectCoolDown({
        maxLat: maxLat,
        maxLng: maxLng,
        finishTime: Date.now() + 10000
      })
      // 更改資料庫mapGridData資料
      const gridData = this.mapGridData.find(ele => {
        return ele.properties.maxLat === maxLat && ele.properties.maxLng === maxLng
      })
      if (gridData) { // 原本該格有存在資料庫中
        gridData.properties.density++
        this.storageToResource(gridData)
      } else { // 原本沒存在，需要增加新的物件於陣列中，並刪除定量資源
        let newGrid = {
          'type': 'Feature',
          'properties': {
            'maxLat': maxLat,
            'maxLng': maxLng,
            'name': 'test',
            'density': 0,
            'respawnTime': null,
            'storages': [
              {
                'type': '木頭',
                'amount': 2000
              },
              {
                'type': '石頭',
                'amount': 1600
              },
              {
                'type': '鋼鐵',
                'amount': 1000
              },
              {
                'type': '銅礦',
                'amount': 400
              }
            ]
          },
          'geometry': {
            'type': 'Polygon',
            'coordinates': [
              [
                [maxLng, maxLat],
                [maxLng - this.gap, maxLat],
                [maxLng - this.gap, maxLat - this.gap],
                [maxLng, maxLat - this.gap],
                [maxLng, maxLat]
              ]
            ]
          }
        }
        // newGrid.properties.density++
        this.ADD_GRIDDATA(newGrid)
        this.storageToResource(newGrid)
      }
      // 判斷玩家該點有沒有在任何古蹟範圍上，有搜集的話加入
      this.addBaggageItems(this.ifOnHistoricalRegion(this.visibleHistoricalSiteInfo))

      this.centerOn() // 回到定位中心
    },
    async storageToResource (gridData) {
      const user = this.userData
      const storages = gridData.properties.storages
      let resources = []
      storages.forEach((storage, index) => {
        let addAmount = user.collectAbility * user.collectWeights[index].weights
        if ((storage.amount - addAmount) < 0) {
          addAmount = storage.amount
        }
        resources.push({
          type: storage.type,
          amount: addAmount
        })
      })
      await this.costResources({ gridData, resources })
      await this.$store.dispatch('user/collectResources', resources)
      this.geojson.addData(this.createVisiableAreaData())
      this.shutdownGrid(gridData)
    },
    ifOnHistoricalRegion (obj) {
      let historicalArr = obj.features.filter((feature) => {
        return this.mymap.distance({ lat: this.lat, lng: this.lng }, { lat: feature.geometry.coordinates[1], lng: feature.geometry.coordinates[0] }) < feature.properties.radius
      })
      let resultItem = []
      if (historicalArr.length > 0) {
        historicalArr.map(feature => {
          if (Math.random() <= feature.drop.rate) {
            resultItem.push({
              id: feature.drop.itemId,
              name_zh: feature.drop.item_zh
            })
          }
        })
      }
      return resultItem
    },
    shutdownGrid (gridData) {
      let stillHaveStorages = gridData.properties.storages.filter(ele => ele.amount > 0).length
      if (stillHaveStorages === 0) {
        this.respawnGridData(gridData)
        setTimeout(() => {
          this.geojson.addData(this.createVisiableAreaData())
        }, this.getRespawnTime)
      }
    },
    centerOn () {
      this.mymap.panTo([this.lat, this.lng])
    },
    updateLocalGeoJson (data) {
      if (data.properties.respawnTime !== null && data.properties.respawnTime >= Date.now()) {
        console.log('大於')
        let time = data.properties.respawnTime - Date.now()
        setTimeout(() => {
          this.geojson.addData(this.createVisiableAreaData())
        }, time)
      }
    },
    closeReport (action) {
      this.isCollecting = false
    }
  }
}
</script>

<style lang='scss' scoped>
@import "@/assets/scss/variables.scss";
.collect-root {
  height: 100%;
  width: 100%;
  position: fixed;
  color: black;
  .cover-layer {
    height: 100%;
    width: 100%;
    background: black;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: map-get($colors, white);
    > img {
      mix-blend-mode: screen;
    }
  }
  #mapid {
    height: 100%;
    width: 100%;
    z-index: 100;
    /deep/ .marker-icon {
      top: 0;
      animation: markerMove 1s infinite;
    }
    /deep/ .historical-region {
      opacity: 1;
      animation: opacityWave 4s infinite;
    }
    @keyframes opacityWave {
      0% {
        opacity: 1;
      }
      50% {
        opacity: 0.5;
      }
      100% {
        opacity: 1;
      }
    }
    @keyframes markerMove {
      0% {
        top: 0;
      }
      50% {
        top: -5px;
      }
      100% {
        top: 0;
      }
    }
    /deep/ .leaflet-popup-content-wrapper {
      border-radius: 0.5rem;
      .leaflet-popup-content {
        margin: 0;
        padding: 0.8125rem 1rem;
        padding-left: 0.8125rem;
        > h3 {
          text-align: center;
          color: map-get($colors, black);
        }
      }
    }
  }
}
</style>
