<template lang="pug">
  div(class="baggage-detail-root")
    div
      div(class="heading-default")
        h3 {{baggageName}}
    div(v-if="noAnyOneStock" class="no-stocks")
      h5 目前背包中沒有任何物件
    div(v-else class="have-stocks")
      img(v-for="(stock,index) in baggage" :src="transformImage.things(stock.name_zh)" @click="showDetail(stock)")
    modal-card-content(v-if="isShowDetail" @close="isShowDetail = !isShowDetail")
      stock-detail(slot="card" :stockId="stockId" :showBottomInfo="false" :showRarity="true")
</template>

<script>
import transformImage from '@/assets/javascript/transformImage.js'
import ModalCardContent from '@/components/ModalCardContent.vue'
import StockDetail from '@/components/game/warehouse/StockDetail.vue'
import { mapGetters } from 'vuex'

export default {
  components: {
    StockDetail,
    ModalCardContent
  },
  props: {
    baggageName: String
  },
  data () {
    return {
      transformImage: transformImage,
      isShowDetail: false,
      stockId: '',
      baggage: []
    }
  },
  created () {
    if (this.baggageName === '物品背包') {
      this.baggage = this.userBaggage.goods
    } else if (this.baggageName === '設計圖背包') {
      this.baggage = this.userBaggage.sketch
    } else {
      this.baggage = this.userBaggage.expendables
    }
  },
  computed: {
    ...mapGetters('user', {
      userBaggage: 'getBaggage'
    }),
    noAnyOneStock () {
      return this.baggage.length === 0
    }
  },
  methods: {
    showDetail (stock) {
      this.isShowDetail = true
      this.stockId = stock.id
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";
.baggage-detail-root {
  display: grid;
  grid-auto-flow: row;
  grid-template-rows: 31px 15px 1fr;
  grid-template-columns: 1fr;
  grid-template-areas:
    "heading"
    "."
    "content";
  > div:nth-child(1) {
    grid-area: heading;
    box-sizing: border-box;
    padding-bottom: 21px;
  }
  .have-stocks {
    grid-area: content;
    display: grid;
    grid-template-columns: 55px 55px 55px 55px;
    grid-auto-rows: 55px;
    grid-gap: 18px 19px;
    > img {
      width: 55px;
      height: 55px;
      background-color: #ffffff;
      background-size: cover;
    }
  }
  .no-stocks {
    grid-area: content;
    > h5 {
      position: relative;
      top: 50%;
      font-size: 14px;
      color: map-get($colors, white);
    }
  }
}
</style>
