<template lang="pug">
  tab(
    class="building-root"
    :navList="navList"
    @switchMenu="switchMenu")
    div(v-if="haveDevelopedAnyOne && currentOpen !== '特色建築'" slot="content" class="normal-mode")
      div(class="selected-building")
        h3 {{selectedBuilding.name_zh}}
        p {{selectedBuilding.description}}
        div(class="building-detail")
          div
            img(:src="transformImage.building(selectedBuilding.name_zh)")
          div
            div
              span 建造格數
              span {{selectedBuilding.grid.row}}X{{selectedBuilding.grid.column}}
            div
              span 建造時間
              span {{convertBuildTime(selectedBuilding.buildTime)}}
            div(:class="{ coinNotEnough : checkCoinEnough(selectedBuilding.cost) } ")
              span 花費金幣
              span {{selectedBuilding.cost}}
        div(class="resource-group")
          div(v-for="resource in selectedBuilding.resources" :class="{ resourceNotEnough : checkResourceEnough(resource)} ")
            i(:style="{'background-image': `url(${transformImage.things(resource.type)})`}")
            span {{resource.amount}}
        button(@click.stop="onBuildMode(selectedBuilding)" class="button-default" :style="isEnough ? 'opacity: 1' : 'opacity: 0.4'") 建造該建築
      div
        div(v-for="building in buildings[currentOpen]")
          img(
            v-if="planet.architectureTechnology[currentOpen_Eg[currentOpen]][building.id]"
            :src="transformImage.building(building.name_zh)"
            :style="selectedBuilding.name_zh === building.name_zh ? 'opacity: 1' : 'opacity: 0.5'"
            @click="chooseBuilding(building)")
    div(v-else-if="currentOpen === '特色建築' && !isOpenSpecial && (haveCollectAnyOneSpecial || haveCollectAnyOneHistorical) " slot="content" class="special-mode")
      div(v-if="haveCollectAnyOneSpecial")
        div(class="heading-default")
          h4 特色建築
        div(class="building-img-wrapper")
          template(v-for="building in buildings[currentOpen]")
            img(
              v-if="planet.architectureTechnology[currentOpen_Eg[currentOpen]].special[building.id]"
              :src="transformImage.building(building.name_zh)"
              @click="chooseBuilding(building)"
            )
      div(v-if="haveCollectAnyOneHistorical")
        div(class="heading-default")
          h4 名勝古蹟
        div(class="building-img-wrapper")
          template(v-for="building in buildings[currentOpen]")
            img(
              v-if="planet.architectureTechnology[currentOpen_Eg[currentOpen]].historical[building.id]"
              :src="transformImage.building(building.name_zh)"
              @click="chooseBuilding(building)"
            )
    div(v-else slot="content")
      div(class="unDevelop")
        p 尚未研發任何{{currentOpen.slice(0, 2)}}建築
    div(v-if="isOpenSpecial" @close="isOpenSpecial = !isOpenSpecial" slot="content" class="special-building")
      h3 {{selectedBuilding.name_zh}}
      div(class="building-detail")
        div
          img(:src="transformImage.building(selectedBuilding.name_zh)")
        div
          div
            span 建造格數
            span {{selectedBuilding.grid.row}}X{{selectedBuilding.grid.column}}
          div
            span 建造時間
            span {{convertBuildTime(selectedBuilding.buildTime)}}
          div(:class="{ coinNotEnough : checkCoinEnough(selectedBuilding.cost) } ")
            span 花費金幣
            span {{selectedBuilding.cost}}
      p {{selectedBuilding.description}}
      div(class="resource-group")
        div(v-for="resource in selectedBuilding.resources" :class="{ resourceNotEnough : checkResourceEnough(resource)} ")
          i(:style="{'background-image': `url(${transformImage.things(resource.type)})`}")
          span {{resource.amount}}
      button(@click.stop="onBuildMode(selectedBuilding)" class="button-default" :style="isEnough ? 'opacity: 1' : 'opacity: 0.4'") 建造該建築
      div(class="leave")
        button(class="icon-button" @click="backToSelecet")
        label 返回
</template>

<script>
import Tab from '@/components/Tab.vue'
import ModalCardContent from '@/components/ModalCardContent.vue'
import buildingInfo from '@/assets/data/building-info.json'
import transformImage from '@/assets/javascript/transformImage.js'
import { mapGetters, mapMutations } from 'vuex'

let buildGrid = {
  row: 1,
  column: 1
}

export default {
  components: {
    Tab,
    ModalCardContent
  },
  props: {
    user: Object,
    planet: Object
  },
  data () {
    return {
      transformImage: transformImage,
      navList: ['通用建築', '軍事建築', '經濟建築', '特色建築'],
      currentOpen: '通用建築',
      selectedBuilding: {},
      buildings: {
        '通用建築': buildingInfo.commonBuilding,
        '軍事建築': buildingInfo.militaryBuilding,
        '經濟建築': buildingInfo.economicBuilding,
        '特色建築': buildingInfo.specialBuilding
      },
      currentOpen_Eg: {
        '通用建築': 'commonBuilding',
        '軍事建築': 'militaryBuilding',
        '經濟建築': 'economicBuilding',
        '特色建築': 'specialBuilding'
      },
      isEnough: true,
      isOpenSpecial: false
    }
  },
  created () {
    this.selectedBuilding = this.buildings['通用建築'][0]
    buildGrid.row = this.selectedBuilding.grid.row
    buildGrid.column = this.selectedBuilding.grid.column
  },
  computed: {
    ...mapGetters('user', {
      isBeginner: 'getIsBeginner',
      currentScene: 'getCurrentBeginnerGuideScenes'
    }),
    haveDevelopedAnyOne () {
      return Object.keys(this.planet.architectureTechnology[this.currentOpen_Eg[this.currentOpen]]).length >= 1
    },
    haveCollectAnyOneSpecial () {
      return Object.keys(this.planet.architectureTechnology[this.currentOpen_Eg[this.currentOpen]].special).length >= 1
    },
    haveCollectAnyOneHistorical () {
      return Object.keys(this.planet.architectureTechnology[this.currentOpen_Eg[this.currentOpen]].historical).length >= 1
    }
  },
  methods: {
    ...mapMutations('feature', ['SHOW_NOTIFICATION']),
    onBuildMode (selectedBuilding) {
      if (this.isBeginner) {
        if (this.currentScene === 16 && selectedBuilding.name_zh !== '倉庫') { return }

        if (this.currentScene === 37 && selectedBuilding.name_zh !== '資源傳輸中心') { return }

        if (this.currentScene === 67 && selectedBuilding.name_zh !== '住宅') { return }

        if (this.currentScene === 85 && selectedBuilding.name_zh !== '市政中心') { return }
      }
      if (!this.isEnough) { return }

      if (this.checkOnlyOne()) { return this.SHOW_NOTIFICATION(`${this.selectedBuilding.name_zh}只能蓋一棟`) }
      this.$emit('close')
      this.$emit('onBuildMode', selectedBuilding)
    },
    switchMenu (name) {
      this.$bus.$emit('nextScenes', '切換' + name)
      this.currentOpen = name
      this.selectedBuilding = this.buildings[this.currentOpen][0]
      buildGrid.row = this.selectedBuilding.grid.row
      buildGrid.column = this.selectedBuilding.grid.column
      this.isOpenSpecial = false
    },
    chooseBuilding (building) {
      this.selectedBuilding = building
      buildGrid.row = building.grid.row
      buildGrid.column = building.grid.column
      this.isEnough = true
      if (this.currentOpen === '特色建築') {
        this.isOpenSpecial = true
      }
    },
    backToSelecet () {
      this.isOpenSpecial = false
    },
    checkResourceEnough (resource) {
      const userResource = this.user.resources.find(item => item.type === resource.type)
      if (userResource.amount < resource.amount) {
        this.isEnough = false
        return true
      }
      return false
    },
    checkCoinEnough (cost) {
      if (this.user.coin.amount < cost) {
        this.isEnough = false
        return true
      }
      return false
    },
    convertBuildTime (myBuildTime) {
      myBuildTime = myBuildTime * 60
      let day = parseInt(myBuildTime / 60 / 60 / 24)
      let hr = String(parseInt(myBuildTime / 60 / 60 % 24)).padStart(2, '0')
      let min = String(parseInt(myBuildTime / 60 % 60)).padStart(2, '0')
      let sec = String(parseInt(myBuildTime % 60)).padStart(2, '0')
      let buildTime = day > 0 ? `${day}天` : ''
      buildTime += `${hr}:${min}:${sec}`
      return buildTime
    },
    checkOnlyOne () {
      if (!this.selectedBuilding.isOnlyOne) { return false }

      return this.planet.buildingMap.find(building => building.id === this.selectedBuilding.id)
    }
  }
}

export { buildGrid }
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.building-root {
  /deep/ .nav {
    span {
      font-size: 11px;
      line-height: 11px;
    }
  }
  > .normal-mode {
    display: grid;
    grid-template-rows: 344px 93px;
    > .selected-building {
      box-sizing: border-box;
      border-bottom: 1px solid map-get($colors, primary);
      display: grid;
      grid-template-rows: 20px 27px 34px 145px 66px 14px 25px 13px;
      grid-template-areas:
        "title"
        "."
        "description"
        "building"
        "resource"
        "."
        "build"
        ".";
      > h3:nth-child(1) {
        grid-area: title;
        font-size: 20px;
        line-height: 20px;
        text-shadow: map-get($shadow, secondary-8);
      }
      > p:nth-child(2) {
        grid-area: description;
        text-align: center;
        font-size: 10px;
      }
      > div:nth-child(3) {
        grid-area: building;
      }
      > div:nth-child(4) {
        grid-area: resource;
      }
      > button:nth-child(5) {
        grid-area: build;
        justify-self: center;
        width: 97px;
        height: 25px;
        font-size: 12px;
      }
    }
    > div:nth-child(2) {
      overflow-x: scroll;
      overflow-y: hidden;
      display: grid;
      grid-auto-columns: 70px;
      grid-template-rows: 93px;
      grid-auto-flow: column;
      grid-column-gap: 10px;
      align-items: flex-end;
      > div {
        > img {
          width: 100%;
        }
      }
      &::-webkit-scrollbar {
        display: none;
        // width: 0 !important;
        -ms-overflow-style: none;
        overflow: -moz-scrollbars-none;
      }
    }
  }
  > .special-mode {
    > div:nth-child(1),
    div:nth-child(2) {
      .building-img-wrapper {
        display: grid;
        grid-template-columns: 53px 53px 53px 53px;
        grid-auto-rows: 53px;
        grid-gap: 13px 14px;
        justify-content: center;
        align-items: flex-end;
        margin: 17px 0;
        > img {
          width: 100%;
        }
      }
    }
  }
  .special-building {
    position: absolute;
    top: 60px;
    box-sizing: border-box;
    display: grid;
    grid-template-rows: 20px 10px 145px 15px 117px 15px 66px 11px 25px 13px;
    grid-template-areas:
      "title"
      "."
      "building"
      "."
      "description"
      "."
      "resource"
      "."
      "build"
      ".";
    > h3:nth-child(1) {
      grid-area: title;
      font-size: 20px;
      line-height: 20px;
      text-shadow: map-get($shadow, secondary-8);
    }
    > div:nth-child(2) {
      grid-area: building;
    }
    > p:nth-child(3) {
      grid-area: description;
      text-align: center;
      font-size: 10px;
    }
    > div:nth-child(4) {
      grid-area: resource;
    }
    > button:nth-child(5) {
      grid-area: build;
      justify-self: center;
      width: 97px;
      height: 25px;
      font-size: 12px;
    }
    .leave {
      position: absolute;
      bottom: -94px;
      left: 50%;
      transform: translateX(-50%);
      background-color: rgba($color: map-get($colors, black), $alpha: 0.9);
      background-clip: content-box;
      > button {
        display: block;
        width: 37px;
        height: 37px;
        background-image: url("~@/assets/icon/back-white.png");
        padding: 0px;
        // FOR TEST
        // position: fixed;
        // bottom: 150px;
      }
      > label {
        text-shadow: map-get($shadow, secondary-4);
        margin-top: 7px;
        font-size: 12px;
      }
    }
  }
}

.unDevelop {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
  > p {
    font-size: 14px;
    text-shadow: map-get($shadow, secondary-4);
  }
}

.resourceNotEnough {
  border: 1px solid #ff417c;
  > span {
    color: #ff417c;
  }
}

.coinNotEnough {
  > span {
    color: #ff417c;
  }
}
</style>
