<template lang="pug">
  div(class="dialog-root" :style="dialogData[currentScenes].position" @click.stop="$emit('nextScenes', 'dialog')")
    div(
    class="dialog-wrapper"
    :style="dialogData[currentScenes].style")
      span 拉維多長老
      p(v-html="dialogData[currentScenes].message")
      i(v-if="dialogData[currentScenes].dialog" :style="{'background-image': `url(${require('@/assets/image/beginnerGuide/touch-dot.png')})`}")
      div
        img(:src="require('@/assets/image/beginnerGuide/old-man.png')")
</template>

<script>
export default {
  props: {
    currentScenes: Number,
    planet: Object
  },
  data () {
    return {
      dialogData: [
        {
          dialog: true,
          position: {
            bottom: '130px'
          },
          style: {
            height: '82px'
          },
          message: `
            歡迎來到${this.planet.name}星球</br>
            我接下來將一步一步帶領你</br>
            建造屬於自己的星球文明。
          `
        }, // 0 dialog
        {
          dialog: false,
          position: {
            bottom: '130px'
          },
          style: {
            height: '64px'
          },
          message: `
           打造城市，從研發開始！</br>
            請點擊圖面上「科研中心」建築。
          `
        }, // 1 點擊科研中心
        {
          dialog: false,
          position: {
            bottom: '130px'
          },
          style: {
            height: '64px'
          },
          message: `
            現在請先點擊右下方「功能」按鍵，</br>
            進入科研中心的建築功能頁。
          `
        }, // 2 建築功能
        {
          dialog: true,
          position: {
            bottom: '34px'
          },
          style: {
            height: '107px'
          },
          message: `
            每個建築的功能不一樣，</br>
            由「科研中心」為例，</br>
            其功能為研發新科技，</br>
            並且解鎖各種建築物的建造能力。
          `
        }, // 3 dialog
        {
          dialog: false,
          position: {
            bottom: '34px'
          },
          style: {
            height: '82px'
          },
          message: `
            讓我們先點擊最上方的＿＿＿＿圖示吧！
          `
        }, // 4 研發倉庫
        {
          dialog: false,
          position: {
            bottom: '34px'
          },
          style: {
            height: '82px'
          },
          message: `
            點擊「進行研發」，</br>
            將可以對「潘朵拉的盒子」進行研究，</br>
            並且獲得建造「倉庫」的能力。
          `
        }, // 5 進行研發
        {
          dialog: false,
          position: {
            bottom: '130px'
          },
          style: {
            height: '64px'
          },
          message: `
            點擊「離開」，</br>
            返回遊戲主畫面。
          `
        }, // 6 離開
        {}, // 7 ***********儲存***********
        {
          dialog: false,
          position: {
            bottom: '130px'
          },
          style: {
            height: '64px'
          },
          message: `
            現在請再次點擊「建造」按鈕，</br>
            讓我們來興建「倉庫」。
          `
        }, // 8 建造頁面
        {
          dialog: false,
          position: {
            bottom: '34px'
          },
          style: {
            height: '82px'
          },
          message: `
            點擊下方的縮圖即可切換建築，</br>
            找到「倉庫」，</br>
            並且按下「建造該建築」吧！
          `
        }, // 9 建造該建築
        {
          dialog: false,
          position: {
            bottom: '130px'
          },
          style: {
            height: '82px'
          },
          message: `
            點擊地圖上其中還沒有建築的位置，</br>
            並點擊下方的「建造」按鈕，</br>
            即可進行建造。
          `
        }, // 10 建造下去
        {}, // 11 ***********儲存***********
        {
          dialog: true,
          position: {
            bottom: '130px'
          },
          style: {
            height: '64px'
          },
          message: `
            恭喜您建立了倉庫，</br>
            並且升級到了等級二。'
          `
        }, // 12 dialog
        {
          dialog: true,
          position: {
            bottom: '130px'
          },
          style: {
            height: '99px'
          },
          message: `
            每當玩家進行建造、採集等行為時，</br>
            經驗值會上升，經驗值滿後將會升級，</br>
            等級上升可以獲得寶石、科研點數，</br>
            或是提升土地地盤等獎勵。'
          `
        }, // 13 dialog
        {
          dialog: false,
          position: {
            bottom: '130px'
          },
          style: {
            height: '44px'
          },
          message: `
            現在請先點擊建造完成的建築，啟用倉庫。
          `
        }, // 14 done
        {
          dialog: false,
          position: {
            bottom: '130px'
          },
          style: {
            height: '84px'
          },
          message: `
            現在讓我們來看看，</br>
            剛建造的「倉庫」有哪些功能，<br/>
            請點擊圖面上「倉庫」建築。
          `
        }, // 15 點擊倉庫
        {
          dialog: false,
          position: {
            bottom: '130px'
          },
          style: {
            height: '44px'
          },
          message: `
            點擊右下方「功能」按鈕。
          `
        }, // 16 建築功能
        {}, // 17 資源頁面
        {
          dialog: true,
          position: {
            bottom: '34px'
          },
          style: {
            height: '103px'
          },
          message: `
            這裡可以看到倉庫所存放的資源與物品，</br>
            其中，建築建材的容量有限制，</br>
            需要透過蓋倉庫或者升級倉庫，</br>
            來獲得更多的存放空間。
          `
        }, // 18 dialog
        {
          dialog: true,
          position: {
            bottom: '34px'
          },
          style: {
            height: '141px'
          },
          message: `
            <strong>克金石</strong>是宇宙中最稀有的物質，</br>
            可以運用魔法執行不合常理的操作。</br>
            <strong>金幣</strong>可用於建造或是交易行為。</br>
            <strong>建築建材</strong>則是建造中所需的資源。</br>
            此外，當有收集到特殊的物品時，</br>
            會置放於<strong>背包物品欄</strong>之中。
          `
        }, // 19 dialog
        {
          dialog: true,
          position: {
            bottom: '34px'
          },
          style: {
            height: '101px'
          },
          message: `
            查看庫存除了從「倉庫」建築的</br>
            功能鍵進入外，</br>
            也可以從主畫面左下方的「資源」按鈕</br>
            直接進入喔！
          `
        }, // 20 dialog
        {
          dialog: false,
          position: {
            bottom: '130px'
          },
          style: {
            height: '64px'
          },
          message: `
            點擊「離開」，</br>
            返回遊戲主畫面。
          `
        }, // 21 離開
        {
          dialog: true,
          position: {
            bottom: '130px'
          },
          style: {
            height: '101px'
          },
          message: `
            在建造建築時，</br>
            皆需要各樣的資源，</br>
            其中<strong>木頭</strong>、<strong>石板</strong>、<strong>鋼鐵</strong>與<strong>銅礦</strong>，</br>
            可以在地球中採集獲得。
          `
        }, // 22 dialog
        {
          dialog: true,
          position: {
            bottom: '130px'
          },
          style: {
            height: '64px'
          },
          message: `
            然而隨著人類待在外太空的時間拉長，</br>
            發現一些根本上的問題...
          `
        }, // 23 dialog
        {}, {}, {}, {}, // 24~27 動畫場景
        {
          position: {
            bottom: '130px'
          },
          style: {
            height: '64px'
          },
          message: `
            現在，請點擊下方的「採集」按鈕，</br>
            讓我們回到地球採集資源吧！
          `
        }, // 28 採集頁面
        {
          dialog: true,
          position: {
            bottom: '130px'
          },
          style: {
            height: '82px'
          },
          message: `
            目前在地球上的波波拉，</br>
            狀態趨緩不再失控，</br>
            我們可以在您所在的格子上進行資源採集。
          `
        }, // 29 dialog
        {
          dialog: true,
          position: {
            bottom: '130px'
          },
          style: {
            height: '101px'
          },
          message: `
            左上方為待命中的採集機器人數量，</br>
            與玩家資源存量。</br>
            當仍有剩餘的採集機器人時，</br>
            即可進行採集功能。
          `
        }, // 30 dialog
        {
          dialog: false,
          position: {
            bottom: '130px'
          },
          style: {
            height: '64px'
          },
          message: `
            現在請先點擊定位點所在的格子，</br>
            下方將會顯示該格的地表資訊。
          `
        }, // 31 gridData
        {
          dialog: false,
          position: {
            bottom: '130px'
          },
          style: {
            height: '64px'
          },
          message: `
            接下來按下「進行採集」按鈕，</br>
            將可進行採集。
          `
        }, // 32 採集
        {}, // 33 儲存 ***********儲存***********
        {
          style: {
            opacity: 0
          }
        }, // 34 採集報告
        {
          dialog: false,
          position: {
            bottom: '130px'
          },
          style: {
            height: '64px'
          },
          message: `
            這樣就採集成功了！</br>
            現在請再點選一次定位點所在的格子。
          `
        }, // 35 gridData
        {
          dialog: true,
          position: {
            bottom: '130px'
          },
          style: {
            height: '111px'
          },
          message: `
            採集成功後，你會得到基本資源，</br>
            並且該格會進入冷卻時間、</br>
            採集機器人也會減少一個，</br>
            該格在冷卻結束前，</br>
            無法再對此格進行採集。
          `
        }, // 36 dialog
        {
          dialog: true,
          position: {
            bottom: '130px'
          },
          style: {
            height: '82px'
          },
          message: `
            不過若有剩餘的採集機器人，</br>
            仍可走到其他格子，</br>
            對其他格子進行採集。
          `
        }, // 37 dialog
        {
          dialog: true,
          position: {
            bottom: '130px'
          },
          style: {
            height: '101px'
          },
          message: `
            此外，若該格被採集者們採集殆盡，</br>
            該格的波波拉將會失控，</br>
            無法再度採集資源，</br>
            須等待資源重生後，才可以進行採集。
          `
        }, // 38 dialog
        {}, // 39 儲存 ***********儲存***********
        {
          dialog: true,
          position: {
            bottom: '130px'
          },
          style: {
            height: '101px'
          },
          message: `
            建造新的建築物需要花費金幣，</br>
            那要如何獲得金幣呢？</br>
            「住宅」能提高星球人口上限，</br>
            而人口可為星球帶來金幣。
          `
        }, // 40 dialog
        {
          dialog: true,
          position: {
            bottom: '130px'
          },
          style: {
            height: '84px'
          },
          message: `
            基礎人口增長率為每小時增加10人，</br>
            上限為住宅所提供的人口容量，</br>
            而產值取決於<strong>人口多寡</strong>。
          `
        }, // 41 dialog
        {
          dialog: true,
          position: {
            bottom: '130px'
          },
          style: {
            height: '84px'
          },
          message: `
            想要查看人口及其他星球資訊，</br>
            可以直接點擊星球名稱旁的圖示</br>
            深入了解哦！
          `
        }, // 42 dialog
        {
          dialog: true,
          position: {
            bottom: '130px'
          },
          style: {
            height: '119px'
          },
          message: `
            除了建造、採集、研發之外，</br>
            我們還需要有<strong>執政官</strong>來幫助我們！</br>
            帶給星球更多效益。</br>
            尋找執政官的方式，</br>
            需要透過「<strong>黑帝斯聖殿</strong>」。
          `
        }, // 43 dialog
        {
          dialog: true,
          position: {
            bottom: '130px'
          },
          style: {
            height: '101px'
          },
          message: `
            「黑帝斯聖殿」的位置，</br>
            位於人類的主星 — Kyronus 上，</br>
            到達 Kyronus 唯一的方式是，</br>
            透過「<strong>巨石陣傳送門</strong>」抵達。
          `
        }, // 44 dialog
        {
          dialog: false,
          position: {
            bottom: '130px'
          },
          style: {
            height: '84px'
          },
          message: `
            現在請點擊地圖中「巨石陣傳送門」，</br>
            並點選星號圖示，</br>
            前往「巨石陣傳送門」建築功能頁面。
          `
        }, // 45 點擊巨石陣傳送門
        {
          dialog: false,
          position: {
            bottom: '130px'
          },
          style: {
            height: '44px'
          },
          message: `
            點擊右下方「功能」按鈕。
          `
        }, // 46 建築功能
        {
          dialog: false,
          position: {
            bottom: '34px'
          },
          style: {
            height: '84px'
          },
          message: `
            在傳送門功能頁中，</br>
            可以找到「Kyronus」人類主星，</br>
            點擊「前往」讓我們去一探究竟吧！
          `
        }, // 47 離開
        {
          dialog: false,
          position: {
            bottom: '130px'
          },
          style: {
            height: '84px'
          },
          message: `
            Kyronus星球為第一顆人類移居的星球，</br>
            具有高度發展的文明，</br>
            現在請尋找並點擊「黑帝斯聖殿」。
          `
        }, // 48 點擊黑帝斯聖殿
        {
          dialog: false,
          position: {
            bottom: '130px'
          },
          style: {
            height: '44px'
          },
          message: `
            點擊右下方「功能」按鈕。
          `
        }, // 49 建築功能
        {
          dialog: true,
          position: {
            bottom: '34px'
          },
          style: {
            height: '84px'
          },
          message: `
            這裡是<strong>黑帝斯聖殿</strong>殿堂，</br>
            您可以使用<strong>50顆克金石</strong>進行靈魂審判，</br>
            將可以<strong>召喚隨機角色</strong>。
          `
        }, // 50 dialog
        {
          dialog: false,
          position: {
            bottom: '34px'
          },
          style: {
            height: '64px'
          },
          message: `
            請觸摸雕像下方的<strong>遠古銘文</strong>，</br>
            以進行靈魂審判。
          `
        }, // 51 抽卡
        {
          dialog: false,
          position: {
            bottom: '34px'
          },
          style: {
            height: '64px'
          },
          message: `
            按下確認，</br>
            花費克金石來召喚角色。
          `
        }, // 52 確認
        {}, // 53 ***********儲存***********
        {
          style: {
            opacity: 0
          }
        }, // 54 抽卡動畫
        {
          dialog: true,
          position: {
            bottom: '34px'
          },
          style: {
            height: '101px'
          },
          message: `
            每次進行靈魂審判皆會召喚一個角色，</br>
            這些角色有著各式各樣的<strong>效果</strong>，</br>
            可以將他們安排至<strong>市政中心</strong>中，</br>
            星球將會得到這些效果。
          `
        }, // 55 返回星球
        {}, // 56 離開
        {}, // 57 ***********儲存***********
        {
          dialog: false,
          position: {
            bottom: '130px'
          },
          style: {
            height: '84px'
          },
          message: `
            現在請在圖面中找到並點擊「市政中心」，</br>
            接著再點擊星號「建築功能」，</br>
            進入市政中心的建築功能頁面中。
          `
        }, // 58 點擊市政中心
        {
          dialog: false,
          position: {
            bottom: '130px'
          },
          style: {
            height: '44px'
          },
          message: `
            點擊右下方「功能」按鈕。
          `
        }, // 59 建築功能
        {
          dialog: true,
          position: {
            bottom: '34px'
          },
          style: {
            height: '101px'
          },
          message: `
            我們可以在此安排執政官，</br>
            從黑帝斯聖殿召喚的角色會</br>
            位於下方的執政人選中，</br>
            長按圖像可看到詳細的角色資訊。
          `
        }, // 60 dialog
        {
          dialog: false,
          position: {
            bottom: '34px'
          },
          style: {
            height: '64px'
          },
          message: `
            現在讓我們來安排執政官吧！</br>
            將「執政人選」托移至上方的執政格。
          `
        }, // 61 拖移
        {
          dialog: false,
          position: {
            bottom: '130px'
          },
          style: {
            height: '84px'
          },
          message: `
            已完成執政官的安排，</br>
            該人物角色的能力效果立即發揮。</br>
            接著點選「離開」'
          `
        }, // 62 離開
        {}, // 63 ***********儲存***********
        {
          dialog: true,
          position: {
            bottom: '130px'
          },
          style: {
            height: '111px'
          },
          message: `
            相信你現在對於星球開發已小有心得。</br>
            除了剛介紹的功能外，</br>
            你還可以進入下方選單的「<strong>任務</strong>」，</br>
            或許會讓你對Kyronus有更多的了解。
          `
        }, // 64 dialog
        {
          dialog: true,
          position: {
            bottom: '130px'
          },
          style: {
            height: '84px'
          },
          message: `
            接下來如果要找到我的話，</br>
            我會再Kyronus主星上喔！</br>
            祝你一切順利，先告辭了。
          `
        } // 65 完成
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

.dialog-root {
  // pointer-events: none;
  position: fixed;
  z-index: 300;
  left: 50%;
  transform: translateX(-50%);
  .dialog-wrapper {
    box-sizing: border-box;
    width: 339px;
    padding: 15px 16px;
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0 3px 15px #ffffff;
    text-align: left;
    > span {
      font-size: 14px;
      line-height: 14px;
      font-weight: 700;
      position: absolute;
      top: -18px;
      left: 16px;
    }
    p {
      color: map-get($colors, black);
      font-size: 12px;
      line-height: 18px;
    }
    i {
      position: absolute;
      bottom: 16px;
      right: 10px;
      width: 10px;
      height: 10px;
    }
    > div {
      position: absolute;
      width: 85px;
      height: 226px;
      right: 15px;
      bottom: -24px;
      > img {
        width: 100%;
      }
    }
  }
}
</style>
